import { createRouter, createWebHistory } from "vue-router"
import Login from "../components/loginPage/login.vue"
// import Loading from "../components/loadingPage/loading.vue"
import ApplicationForm from "../components/children/ApplicationForm.vue"
import ApplicationDetail from "../components/SubPage/ApplicationDetail.vue"
import DomesticTicket from "../components/children/DomesticTicket.vue"
import trainticket from "../components/children/Trainticket.vue"
import hotelExpansion from "../components/children/hotelExpansion.vue"
const routes = [
  {
    path: "/",
    name: "login",
    component: Login,
  },
  {
    path: "/login",
    redirect: "/",
  },
  {
    path: "/home",
    redirect: "/index",
  },
  {
    path: "/index",
    name: "index",
    component: () => import("../components/indexPage/index.vue"),
  },
  // 国内
  {
    path: "/flight",
    name: "flight",
    component: () => import("../components/flightPage/flight.vue"),
  },
  // 国际
  {
    path: "/internationalflight",
    name: "internationalflight",
    // redirect: "/flight",
    component: () => import("../components/flightPage/flight.vue"),
  },
  // 火车
  {
    path: "/train",
    name: "train",
    component: () => import("../components/trainPage/train.vue"),
  },
  // 火车列表
  {
    path: "/trainticket",
    name: "Trainticket",
    component: trainticket,
  },
  {
    path: "/trainticket_back",
    name: "Trainticket_back",
    component: trainticket,
  },
  // 酒店
  {
    path: "/hotel",
    name: "hotel",
    component: () => import("../components/hotelPage/hotel.vue"),
  },
  // 酒店列表
  {
    path: "/hotellist",
    name: "hotellist",
    component: () => import("../components/children/hotellist.vue"),
  },
  // 酒店详情
  {
    path: "/hotelExpansion",
    name: "HotelExpansion",
    component: hotelExpansion,
  },
  // 审批单
  {
    path: "/applicationform",
    name: "ApplicationForm",
    component: ApplicationForm,
  },
  {
    path: "/approveform",
    name: "ApproveForm",
    component: ApplicationForm,
  },
  {
    path: "/applicationdetail",
    name: "applicationdetail",
    component: ApplicationDetail,
  },

  // {
  //   path: "/applicationdetail",
  //   name: "applicationdetail",
  //   component: () => import("../components/children/ApplicationForm.vue"),
  // },
  // 隐私条款
  {
    path: "/privacy",
    name: "privacy",
    component: () => import("../components/privacyPage/privacy.vue"),
  },
  // 航班列表
  {
    path: "/domesticTicket",
    name: "domesticTicket",
    component: DomesticTicket,
  },
  {
    path: "/domesticTicket_back",
    name: "DomesticTicket_back",
    component: DomesticTicket,
  },
  // 动态播报
  {
    path: "/dynamic",
    name: "dynamic",
    component: () => import("../components/dynamicPage/dynamic.vue"),
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
})

export default router
