<template>
  <div class="overall">
    <!-- 头部 -->
    <van-sticky>
      <header class="head">
        <div class="head-title">
          <van-icon name="arrow-left" size="23" @click="
              $router.go(-1);
              multipass = null;
            " />
          <div>
            <i>
              <span>{{
                search.cities[now_trip == "0" ? 0 : 1].namecn +
                  "-" +
                  search.cities[now_trip == "0" ? 1 : 0].namecn
              }}</span>
              <span>&nbsp;{{
                  getDay(search.date[now_trip == "0" ? 0 : 1]) +
                    " " +
                    getWeek(search.date[now_trip == "0" ? 0 : 1])
                }}</span>
            </i>
            <i>
              <span>{{
                search.cities[now_trip == "0" ? 0 : 1].nameen +
                  "-" +
                  search.cities[now_trip == "0" ? 1 : 0].nameen
              }}</span>
              <span>&nbsp;{{
                  enDay(search.date[now_trip == "0" ? 0 : 1]) +
                    " " +
                    enMonth(search.date[now_trip == "0" ? 0 : 1])
                }}</span>
            </i>
          </div>
        </div>
      </header>
    </van-sticky>

    <!-- 整体内容 -->
    <article>
      <!-- 去程 -->
      <section v-if="now_trip != 0">
        <div class="flight_list_content">
          <div>
            <div class="flight_conlist_d">
              <div class="fli_i">
                <div class="fli_item">
                  <div class="flight-line_3"><span>去程</span></div>
                  <!--  -->
                  <div class="fli_item_1">
                    <span>{{ choose_train[0].seg.Train_number }}</span>
                  </div>
                  <!--  -->
                  <div class="fli_item_2"></div>
                  <!--  -->
                  <div class="fli_item_3">
                    <i>
                      <span><span style="
                            color: #eb6301;
                            font-size: 15px;
                            font-weight: 600;
                          ">{{ choose_train[0].seg.From_time }}</span>
                        {{ choose_train[0].seg.From_station_data.namecn }}
                        &nbsp;
                      </span>
                      <br />
                      <span>{{
                        choose_train[0].seg.From_station_data.nameen
                      }}</span>
                    </i>
                    <i class="when">{{ choose_train[0].seg.Use_time2 }}</i>
                    <i>
                      <span><span style="
                            color: #eb6301;
                            font-size: 15px;
                            font-weight: 600;
                          ">{{ choose_train[0].seg.To_time }}</span>
                        {{ choose_train[0].seg.To_station_data.namecn }} &nbsp;
                      </span>
                      <br />
                      <span>{{
                        choose_train[0].seg.To_station_data.nameen
                      }}</span>
                    </i>
                    <!-- <i class="price">
                      <span>
                        ￥{{
                          toMoney(parseFloat(choose_train[0].cabin.Seat_price))
                        }}</span
                      >
                    </i> -->
                  </div>

                  <div class="fli_item_4">
                    <i class="price back_price">
                      <span @click="
                          $router.go(-1);
                          multipass = null;
                        ">重选去程</span>
                      <span>￥{{
                          toMoney(parseFloat(choose_train[0].cabin.Seat_price))
                        }}</span>
                    </i>
                  </div>

                  <!--  -->
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <!-- 班次列表开始 -->
      <section v-for="(data, i) in get_train_flight_list" :key="i">
        <div class="flight_list_content" v-if="data.Seats[0]">
          <div>
            <div class="flight_conlist_d">
              <div class="fli_i" @click="onOpen(i)">
                <div class="fli_item">
                  <!--  -->
                  <div class="fli_item_1">
                    <span>
                      <img style="width: 40px; height: 40px" src="../../assets/image/train_icon.png" alt="" />
                    </span>
                    <span>{{ data.Train_number }}</span>
                    <!-- <img
                      width="50"
                      height="20"
                      src="../../assets/image/train/hc.jpeg"
                      alt=""
                    /> -->
                  </div>
                  <div class="fli_item_2"></div>

                  <div class="fli_item_3">
                    <i>
                      <span><span style="
                            color: #eb6301;
                            font-size: 15px;
                            font-weight: 600;
                          ">{{ data.From_time }}</span>
                        {{ data.From_station_data.namecn }} &nbsp;
                      </span>
                      <br />
                      <span>{{ data.From_station_data.nameen }}</span>
                    </i>
                    <i class="when">{{ data.Use_time2 }}</i>
                    <i>
                      <span><span style="
                            color: #eb6301;
                            font-size: 15px;
                            font-weight: 600;
                          ">{{ data.To_time }}</span>
                        {{ data.To_station_data.namecn }} &nbsp;
                      </span>
                      <br />
                      <span>{{ data.To_station_data.nameen }}</span>
                    </i>
                    <i class="notice" @click="Bookings = true">
                      <span>预订须知</span>
                    </i>
                    <i class="price">
                      <span v-if="data.Lowest_price != 10000000">￥{{ toMoney(parseFloat(data.Lowest_price)) }}</span>
                      <span v-else>售罄</span>
                    </i>
                  </div>
                  <!--  -->
                </div>
              </div>
              <!-- 展开列表 -->
              <transition name="fade">
                <!-- <div class="particulars" v-if="i == isOpen"> -->
                <div class="particulars" style="display: none" :ref="i">
                  <div class="par_item" v-for="(value, k) in data.Seats" :key="k">
                    <div>
                      <i>
                        <span style="color: #eb6301; top: 8px">￥{{
                            toMoney(parseFloat(value.Seat_price))
                          }}&nbsp;</span>
                        <span></span>
                        <span></span>
                        <div class="book" @click="choose(data, value, k)">
                          预订/book
                        </div>
                        <span class="par_item_txt">
                          <i>{{ value.Seat_name }}</i>
                        </span>
                        <span class="par_item_txt">
                          <i>座位{{
                              value.Seat_yupiao < 10
                                ? "剩" + value.Seat_yupiao
                                : ">9"
                            }}</i>
                        </span>
                      </i>
                    </div>
                  </div>
                </div>
              </transition>
              <!-- 展开列表结束 -->
            </div>
          </div>
        </div>
      </section>
    </article>
    <!-- 底部工具 -->
    <transition name="bottool">
      <div class="bottom-toolbars" v-if="bomToolFlag">
        <van-grid :border="false" :icon-size="22" :column-num="3">
          <div class="btn-too-box">
            <van-grid-item icon="after-sale" @click="sort_by(0)" :text="sort_text(0)" />
            <span>Price</span>
          </div>
          <div class="btn-too-box">
            <van-grid-item icon="underway-o" @click="sort_by(1)" :text="sort_text(1)" />
            <span>Time</span>
          </div>
          <div class="btn-too-box">
            <van-grid-item icon="filter-o" text="筛选" @click="filtrateShow = true" />
            <span>Filter</span>
          </div>
        </van-grid>
      </div>
    </transition>

    <!--  -->
    <!-- 筛选弹出层 -->
    <van-action-sheet v-model="filtrateShow" close-on-click-action close-icon="close" title="筛选/Filter">
      <hr class="delay-line" />
      <div class="filtrate">
        <van-tree-select :items="filtrates" v-model:main-active-index.sync="active">
          <template #content>
            <div style="text-align: left" v-if="active === 0">
              <div>
                <div class="filtrate-content">
                  <van-checkbox-group v-model="result" ref="checkboxGroup">
                    <span class="unlimited" :class="[isUnlimited ? 'onUnlimited' : 'noUnlimited']" @click="handleUnlimited">
                      <span>不限</span>
                      <span>/unlimited</span>
                    </span>
                    <van-cell-group>
                      <van-cell v-for="(item, index) in all_s" clickable :key="index" :title="item" size="2rem" @click="toggle(index)">
                        <!-- <template>
                          <span>{{ item.te }}</span>
                        </template> -->
                        <template #right-icon>
                          <van-checkbox :name="item" ref="checkboxes">
                            <template #icon="props">
                              <img class="img-icon" :src="props.checked ? activeIcon : inactiveIcon" />
                            </template>
                          </van-checkbox>
                        </template>
                      </van-cell>
                    </van-cell-group>
                  </van-checkbox-group>
                </div>
              </div>
            </div>
          </template>
        </van-tree-select>
        <van-button class="confirm" type="info">确定<br />Confirm</van-button>
      </div>
    </van-action-sheet>
    <!--  -->

    <!-- 成本中心弹出 -->
    <div class="pup_cost" v-if="pupCostShow">
      <van-popup v-model:show="pupCostShow" round position="bottom" :style="{ height: '50%' }">
        <div class="pup_cost_title">
          成本中心/Cost Center
          <span><van-icon name="close" size="23" color="#8B8B8B" @click="pupCostShow = false" /></span>
        </div>
        <div class="quick_search">
          <input type="text" placeholder="快速检索/Quick Search" v-model="search_cclist" />
          <span><van-icon name="search" size="23" color="#8B8B8B" /></span>
        </div>
        <div class="pup_cost_deli">
          <van-radio-group v-model="costRadio">
            <van-cell-group>
              <van-cell v-for="(item, i) in filter_cclist" :key="i" :title="`${i + 1} - ${item.remark}`" clickable @click="isCost(item)">
                <template #icon>
                  <van-radio :name="item.F_JS" />
                </template>
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </div>
        <div class="next" @click="isCostNext()">
          <span>下一步<br />Next</span>
        </div>
      </van-popup>
    </div>
    <!-- 成本中心弹出over -->
    <!-- 在线选择 -->
    <div class="pup_cost choose_pup" v-if="pupSeatChoose">
      <van-popup v-model:show="pupSeatChoose" round position="bottom" :style="{ height: '45%' }">
        <div class="pup_cost_title">
          在线选座/Online options
          <span><van-icon name="close" size="23" color="#8B8B8B" @click="pupSeatChoose = false" /></span>
        </div>

        <div style="height: 50%; overflow: scroll">
          <div class="choose_set" v-for="(item, i) in choose_train" :key="i">
            <div class="">
              <!-- <div v-for="(vv, y) in search_train.psg" :key="y">
                <span>{{ vv.FltPaxnameDom }}</span>
              </div> -->
              <!-- 乘客 -->
              <!-- <div>
                <span style="font-size: 14px">{{
                  search_train.psg[i2].FltPaxnameDom
                }}</span>
              </div> -->
              <!--  -->
              <template v-for="(v, i2) in c_s">
                <div v-if="i2 == 0 || (i2 == 1 && search_train.psg.length > 1)" :kty="i2" :key="i2">
                  <!-- 乘客 -->
                  <div>
                    <span style="font-size: 14px">{{
                        search_train.psg[i2].FltPaxnameDom
                      }}</span>

                    <span v-if="search.router_active == 1">
                      {{ choose_train[i].date }}
                    </span>

                    <span style="margin-left: 5px" v-if="search.router_active == 1">
                      {{ tripsxxx[i] }}
                    </span>
                    <!-- <span v-if="!choose_train[1]">
                      {{choose_train[0].date}}
                    </span>
                    <span v-else>
                      {{choose_train[0].date}}
                    </span> -->
                  </div>
                  <!--  -->
                  <div class="c_s_0">
                    <template v-for="(c, b) in seat" :key="b">
                      <span @click="seat_c(c, i2, i)" class="seat_" :class="{
                            seat_choose: c.length == 1,
                            choosed: c_s[i].indexOf(i2 * 1 + 1 + c) != -1,
                          }">{{ c }}</span>
                    </template>
                  </div>
                  <!--  -->
                </div>
              </template>
            </div>
          </div>
        </div>

        <div class="choose_rule">
          <p>优先按指定坐席出票，若制定坐席无票将转购其他坐席</p>
          <p>
            Ticket will be issued upon your priority choice. if it is not
            available other seat will be issued.
          </p>
        </div>
        <div class="next" style="bottom: 10%;" @click="seatChooseNext()">
          <span>下一步<br />Next</span>
        </div>
      </van-popup>
    </div>

    <!-- 差旅目的弹出 -->
    <div class="pup_cost traveler-box" v-if="Traveler_popup">
      <van-popup v-model:show="Traveler_popup" round position="bottom">
        <div class="pup_cost_title">
          差旅目的/Travel Purpose
          <span><van-icon name="close" size="23" color="#8B8B8B" @click="Traveler_popup = false" /></span>
        </div>
        <div class="traveler-deil">
          <div v-for="(value, i) in choose_train" :key="i">
            <div class="traveler-d-title">
              {{ value.date }}
              {{ value.seg.Train_number }}
              {{ value.seg.Use_time2 }}
            </div>
            <!--  -->
            <div class="traveler-d-route">
              <i class="traveler-d-route-to">
                <span>{{ value.seg.From_time }}</span>
                <span>{{ value.seg.From_station_data.namecn }} </span>
                <span>{{ value.seg.From_station_data.nameen }} </span>
              </i>
              <div class="traveler-d-flight_to">
                <img src="../../assets/image/flight_to.png" alt="" />
              </div>
              <i class="traveler-d-route-from">
                <span>{{ value.seg.To_time }}</span>
                <span>{{ value.seg.To_station_data.namecn }} </span>
                <span>{{ value.seg.To_station_data.nameen }} </span>
              </i>
            </div>
            <!--  -->
            <!-- <div class="traveler_d_set">
              <span>在线选座/Online options</span>
            </div> -->

            <div class="traveler-d-passenger">
              <template v-if="now_trip != 1">
                <i v-for="(item, i) in search_train.psg" :key="i">
                  <span>乘客/Passenger</span>
                  <span>{{ item.FltPaxnameDom }}</span>
                </i>
              </template>

              <i class="bott">
                <span>价格(含税) /Eexpense (Incl Tax)</span>
                <span class="price">￥{{ toMoney(parseFloat(value.cabin.Seat_price)) }}/人person
                  ×{{ search_train.psg.length }} ￥{{
                    toMoney(
                      parseFloat(value.cabin.Seat_price) *
                        search_train.psg.length
                    )
                  }}</span>
              </i>
            </div>
          </div>
          <!--  -->
          <!-- 乘客 -->
          <div class="traveler-d-passenger" v-if="now_trip == 1">
            <template>
              <i v-for="(item, i) in search_train.psg" :key="i">
                <span>乘客/Passenger</span>
                <span>{{ item.FltPaxnameDom }}</span>
              </i>
            </template>

            <i class="bott">
              <span>价格(含税) /Eexpense (Incl Tax)</span>
            </i>

            <i class="price_pre">
              <span class="price">
                ￥{{ toMoney(parseFloat(price_per)) }}
              </span>
            </i>
          </div>
          <!--  -->

          <!--  -->
          <div class="traveler-d-purpose">
            <span class="purpose-text">差旅目的/Travel Purpose</span>

            <div>
              <van-cell>
                <van-dropdown-menu :overlay="false" active-color="#035ABB">
                  <van-dropdown-item v-model="purposeValue" :options="purposeOptions" @change="purposeItem(purposeOptions[purposeValue])" />
                </van-dropdown-menu>
              </van-cell>
            </div>
          </div>
          <!--  -->
          <div class="traveler-d-purpose">
            <span class="purpose-text">支付方式/Payment</span>
            <div>
              <van-cell>
                <van-dropdown-menu :overlay="false" active-color="#035ABB">
                  <van-dropdown-item v-model="paymentValue" :options="paymentOptions" @change="paymenItem(paymentOptions[paymentValue])" />
                </van-dropdown-menu>
              </van-cell>
            </div>
          </div>
          <!--  -->
          <div class="traveler-d-btn">
            <i @click="travelerCancel()"><span>取消</span> <span>Cancel</span></i>
            <i @click="travelerConfirm()"><span>确认</span> <span>Confirm</span></i>
          </div>
          <!--  -->
        </div>
      </van-popup>
    </div>
    <!-- 预订须知 -->
    <div class="pup_cost" v-if="true">
      <van-popup v-model:show="Bookings" round position="bottom" :style="{ height: '50%' }">
        <!-- <van-sticky> -->
        <div class="pup_cost_title">
          12306退改签规则
          <span><van-icon name="close" size="23" color="#8B8B8B" @click="Bookings = false" /></span>
        </div>
        <!-- </van-sticky> -->

        <div class="dx-deil">
          <div class="dx-fieldset">
            <div class="dx-fieldset-header">改期条件：</div>
            <div class="limitchange dx-field" style="padding: 0px 8px;font-size:14px">
              <!-- <p v-html="show_limit[1]"></p> -->
              <p>
                旅客不能按票面指定的乘车站、日期、车次乘车时，未取纸制车票的可在12306手机APP或12306官网改签，已取纸制车票的需到火车站售票大厅办理一次提前或推迟乘车签证（简称改签）。
                在有运输能力的前提下：
              </p>
              <span>
                （1）开车前48小时（不含）以上，可改签预售期内的其他列车；
              </span>

              <span>（2）开车前48小时以内，可改签开车前的其他列车，也可改签开车后至票面日期当日24:00之间的其他列车，不办理票面日期次日及以后的改签；</span>
              <span>（3）开车之后，旅客仍可改签当日其他列车，但只能在票面发站办理改签，且开车后改签的车票不能退票；</span>
              <span>（4）已经办理“变更到站”的车票，不再办理改签；</span>
              <span>（5）开车前48小时～8天期间内，改签至距开车15天以上的其他列车，又在距开车8天前退票的，仍核收5%的退票费；</span>
            </div>
            <div class="dx-fieldset-header">退票规则：</div>
            <div class="limitchange dx-field" style="padding: 0px 8px">
              <p></p>
            </div>
            <div class="dx-fieldset-header">退票说明：</div>
            <div class="limitchange dx-field" style="padding: 0px 8px;font-size:14px">
              <p>
                <span>（1）未换取纸质车票，可不晚于车票发站开车前在网上办理网络退票业务，也可于开车前到车站安装有POS机的窗口办理；</span>
                <span>（2）已换取纸质车票，开车前到车站安装有POS机的窗口办理；</span>
                <span>（3）在票面指定的开车时间前到车站办理，退还全部票价，核收退票费；</span>
                <span>（4）特殊情况经购票地车站或票面乘车站站长同意的，可在开车后2小时内办理，改签后的车票不予办理。</span>
              </p>
            </div>
          </div>
        </div>
      </van-popup>
    </div>

    <div class="pup_cost" v-if="pupRelevanceShow">
      <van-popup v-model:show="pupRelevanceShow" round :style="{
          top:
            v_12306_title == '手机短信核验' && !s12306ve3 && !s12306ve1
              ? 30 + '%'
              : '',
          height:
            v_12306_title == '手机短信核验' && !s12306ve3 && !s12306ve1
              ? 50 + '%'
              : 40 + '%',
        }">
        <div class="pup_cost_title">
          {{ v_12306_title }}
          <span><van-icon name="close" size="23" color="#8B8B8B" @click.stop="(pupRelevanceShow = false), (Traveler_popup = true)" /></span>
        </div>

        <div class="pup_cost_deli" v-if="v_12306_title == '关联选择' && v_s.status == 0">
          <van-radio-group v-model="costRadioR">
            <van-cell-group>
              <template v-for="(item, i) in Association" :key="i">
                <van-cell :title="item.remark" clickable @click="isAssociation(item)">
                  <template #icon>
                    <van-radio :name="item.F_JS" />
                  </template>
                </van-cell>
              </template>
            </van-cell-group>
          </van-radio-group>
        </div>
        <div class="pup_cost_deli" v-if="v_12306_title == '12306登录' && v_s.login">
          <van-form @submit="isRegisterNext">
            <van-cell-group inset>
              <van-field v-model="formx.user" name="12306账号" label="12306账号" placeholder="请填写12306账号" :rules="[{ required: true, message: '' }]" />
              <van-field v-model="formx.pass" type="password" name="12306密码" label="12306密码" placeholder="请填写12306密码" :rules="[{ required: true, message: '' }]" />
            </van-cell-group>
            <div style="padding: 5px;text-align: left;" v-if="checkResultMsgErro">
              <span style="color: red;font-size: 14px;">{{
                checkResultMsgErro
              }}</span>
            </div>
            <div class="next" @click="isRegisterNext()">
              <span>提交<br />Submit</span>
            </div>
          </van-form>
        </div>
        <!-- 需要短信核验 -->
        <div class="pup_cost_deli" v-if="v_s.status == 1.5 && !v_s.login">
          <div style="margin:5px">
            <img :src="apilistx[3] + '/img/tip_v.png'" />
          </div>
          <div style="font-size: 14px;">
            <span style="font-weight:600">根据铁路局规定，你的账号需要先完成短信核验。</span><br />
            <div style=" display: flex;justify-content: center;align-items: center;">
              <span class="login_v_s">可能会验证失败的原因</span><span class="icon_vt" style="display: inherit;"><van-icon name="warning" color="#1989fa" /></span>
            </div>
          </div>
          <div class="next" @click="isRegisterNext()">
            <span>去核验<br />Verification</span>
          </div>
        </div>

        <!-- 核验成功 -->
        <div class="pup_cost_deli pup_cost_succ" v-if="s12306ve1 && v_s.status == 3">
          <div style="margin:5px">
            <img :src="apilistx[3] + '/img/yes.png'" />
          </div>
          <div style="font-size: 14px;">
            <span style="font-weight:600">{{ check_123_success }}</span><br />
          </div>
          <div class="next" @click="isRegisterNext()">
            <span>提交订单<br />Submit</span>
          </div>
        </div>
