  created () {
    this.Association.map((item, i, arr) => {
      i = `${i}`;
      item.F_JS = i;
    });
    this.stutas1();
    if (sessionStorage.getItem("cclist")) {
      this.travel_setx = JSON.parse(sessionStorage.getItem("cclist"));
      this.travel_setx.map((item, i, arr) => {
        i = `${i}`;
        item.F_JS = i;
      });
    } else {
      this.getCclist();
    }
    if (!sessionStorage.getItem("search_train")) {
      if (this.$route.path == "/trainticket") this.$router.go(-1);
      else this.$router.go(-2);
    } else {
      let f = JSON.parse(sessionStorage.getItem("search_train"));
      this.search = f;
    }
    this.getFlightList();
    console.log('班次查询信息', this.search);

    if (
      this.search.router_active == 1 &&
      (this.now_trip == 0 || this.now_trip == 1)
    ) {
      this.multipass = null;
