    console.log(
      "单程、返程",
      this.search.router_active,
      this.now_trip,
      this.multipass
    );
    let ishttps = this.getBaseUrl() ? "s" : "";
    let apilistx = [
      "http" + ishttps + "://ezway.efasco.com/ErpService.asmx/",
