        "http" + ishttps + "://timas.efasco.com/wxapp",
      ];
    }
    if (hosts.indexOf("porsche") != -1) {
      apilistx = [
        "http" + ishttps + "://porsche.efasco.com/ErpService.asmx/",
        "http://192.168.0.206:8095/Service1.asmx/",
        "http" +
        ishttps +
        "://porsche.efasco.com/ErpService.asmx/SavePorscheTrainTrips",
        "http" + ishttps + "://timas.efasco.com/wxapp",
      ];
