      apilistx[2] =
        "https://cwellsapi.casonline.cn/apiservice/apiservice.asmx/SaveBQTrainTrips";
    }
    if (window.location.host.indexOf("cwellsapi.casonline.cn/webtest/") != -1) {
      apilistx[0] =
        "https://cwellsapi.casonline.cn/ShuntuTrainService/Service1.asmx/";
      apilistx[2] =
        "https://cwellsapi.casonline.cn/apiservice/apiservice.asmx/SaveBQTrainTrips";
    }
    if (window.location.host.indexOf("cwellsapi.casonline.cn/webapp") != -1) {
      apilistx[0] =
        "https://cwellsapi.casonline.cn/ShuntuTrainService/Service1.asmx/";
      apilistx[2] =
        "https://cwellsapi.casonline.cn/apiservice/apiservice.asmx/SaveBQTrainTrips";
    }
    this.apilistx = apilistx;
  },
  mounted () {
    //挂载完成
    window.addEventListener("scroll", this.scrollH);
  },

  destroyed () {
    //销毁后
    window.removeEventListener("scroll", this.scrollH);
  },

  watch: {
    now_trip (e) {
      this.cache_flightlist(); //
    },
    "$route.path" (e) {
      if (e == "/trainticket") {
        this.now_trip = 0;
      } else {
        this.now_trip = 1;
      }
    },

    Traveler_popup (e) {
      if (e) {
        this.price_one += parseFloat(this.choose_train[0]);
        this.choose_train.map((v, i) => {
          this.price_per += v.cabin.Seat_price * this.search_train.psg.length;
        });
      } else {
        this.price_per = 0;
      }
    },

    get_train_flight_list (e) {
      let _this = this;
      let b = null;
      if (e) {

        b = e.filter((v) => {
          // console.log('火车班车', v);
          if (v.Seats.length > 0) {
            return true;
          }
        });
        // console.log('火车列表', b);

        if (b.length <= 0) {
          showDialog({
            title: "提示/Notice",
            message:
              "该坐等已售罄，请返回选择其他坐等/This seat is sold out. Please return to another seat.",
            theme: "round-button",
            confirmButtonText: "确定/Confirm",
            confirmButtonColor:
              "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
          }).then(() => {
            this.$router.go(-1);
          });
        }
      }

    },

  },
  computed: {
    // get_filter_flight_list() {
    //   return this.FlightList.filter((v, i) => {
    //     let c = true;

    //     return c;
    //   });
    // },

    get_train_flight_list () {
      return this.FlightList.filter((v) => {
        // console.log('新数据this.FlightList', this.FlightList);
        const arr1 = v.allseats,
          arr2 = this.result,
          //new Set()数组去重
          _arr1Set = new Set(arr1),
          _arr2Set = new Set(arr2);
        if (arr2.length == 0) return true;
        //    arr1.filter((item)=>{
        //    console.log(_arr2Set.has(item))
        //  })

        //has() 将匹配元素集合缩减为拥有匹配指定选择器或 DOM 元素的后代的子集
        return arr1.filter((item) => _arr2Set.has(item)).length > 0;
      });
    },
    filter_cclist () {
      if (!this.search_cclist) return this.travel_setx;
      console.log(this.travel_setx);
      return this.travel_setx.filter((v) => {
        return v.descr.indexOf(this.search_cclist) != -1;
      });
    },
  },
  methods: {
    getBaseUrl () {
      //   返回当前协议  http 或者  https
      let ishttps = "https:" == document.location.protocol ? true : false;
      return ishttps;
    },
    stutas1 () {
      let formx = {
        user: "", //hrl0513 hrl1984
        pass: "",
      };
      let p = this.AssociationList;
      if (!this.formx.user && !this.formx.pass) {
        if (p == "乘车人本人账号") {
          formx.user = this.booker.TrainAccount;
          formx.pass = this.booker.TrainPassword;
        }
        if (p == "代订人账号") {
          formx.user = "";
          formx.pass = "";
        }
      } else {
        formx.user = this.formx.user;
        formx.pass = this.formx.pass;
      }

      // if (CltId_set.notneed_save_12306) {
      //   formx.user = "";
      //   formx.pass = "";
      // }
      this.formx = formx;
      console.log('this.formx', this.formx);
      console.log('this.booker', this.booker);
      console.log('p', p);
    },
    scrollH () {
      const that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;

      var scroll = scrollTop - that.scrollTop;
      that.scrollTop = scrollTop;

      if (that.scrollTop > 120 && scroll > 0) {
        // setTimeout(() => {
        //   that.bomToolFlag = false;
        // }, 500);
        that.bomToolFlag = false;

        // console.log("down"); //向下滑
      } else {
        // setTimeout(() => {
        //   that.bomToolFlag = true;
        // }, 1000);
        that.bomToolFlag = true;

        // console.log("up"); //向上滑
      }
    },
    goBack () {
      this.$router.go(-1);
    },
    getCclist () {
      axios({
        method: "GET",
        url:
          "https://timas.efasco.com/ErpService.asmx/GetCostcenterByClientId?cltid=" +
          this.booker.CltId,
        cache: "false",
        dataType: "json",
      }).then((res) => {
        this.travel_setx = res.data;
        sessionStorage.setItem("cclist", JSON.stringify(res.data));
        this.travel_setx.map((item, i, arr) => {
          i = `${i}`;
          item.F_JS = i;
        });
        console.log(this.travel_setx);
      });
    },
    cache_flightlist () {
      //去程数据重新加载
      console.log(
        "look",
        this.now_trip,
        sessionStorage.getItem("getlist_" + this.now_trip)
      );
      if (sessionStorage.getItem("getlist_" + this.now_trip)) {
        console.log("true");
        let g = sessionStorage.getItem("getlist_" + this.now_trip);
        let g2 = new Date().getTime();
        if (g2 - g > this.cache_limit * 60 * 1000) {
          //缓存超时
          this.getFlightList();
        } else {
          this.isOpen = -1;
          let g3 = JSON.parse(
            sessionStorage.getItem("savelist_" + this.now_trip)
          );

          this.filterInfo(g3);
          // console.log('true', g3)
        }
      } else {
        this.getFlightList();
        console.log("else");
      }
    },
    getFlightList () {
      // this.upload();
      this.isOpen = -1;
      let _this = this;
      let datex = this.search.date[this.now_trip == "0" ? 0 : 1];
      axios({
        method: "GET",
        url:
          "https://timas.efasco.com/TRAIN/Service1.asmx/TrainTickets?date=" +
          datex +
          // "http://timas.efasco.com/TRAIN/Service1.asmx/TrainTicketsGD?date="+datex+
          "&from=" +
          encodeURI(this.search.cities[this.now_trip == "0" ? 0 : 1].namecn) +
          "&to=" +
          encodeURI(this.search.cities[this.now_trip == "0" ? 1 : 0].namecn) +
          "&trainNo=",
      })
        .then((res) => {
          console.log('航班列表', res.data);
          // _this.$toast.clear();
          if (!res.data && res.data == 'null') {
            showDialog({
              title: "提示/Notice",
              message:
                "未查询到指定航班,请更换条件!/The specified flight has not been queried, please change the conditions!",
              theme: "round-button",
              confirmButtonText: "确定/Confirm", //改变确认按钮上显示的文字
              confirmButtonColor:
                "linear-gradient(160deg, #0274c9 0%, #044db5 100%)", //确定按钮文字
            }).then(() => {
              this.$router.go(-1);

            });
          }
          if (res.status == 200 && typeof res.data == 'object') {
            sessionStorage.setItem("getlist_t_" + _this.now_trip, new Date().getTime());
            sessionStorage.setItem("savelist_t" + _this.now_trip, JSON.stringify(res.data));
          }
          _this.filterInfo(res.data);
          // _this.$toast.clear();
        })
        .catch((e) => {
          console.log(e);
          // _this.$toast.clear();
        });
    },
    //处理数据字段
    filterInfo (data) {
      this.skeletonScreenLength = data.length
      this.FlightList = data;
      let all_s = (this.all_s = []);
      data.map((item, i) => {

      });
      let _this = this;
      data.map((v) => {
        // v.Seats = v.Seats.filter((x)=>{
        //   return x.Seat_yupiao !=0;
        // });
        //座位等级筛选
        let canbislist = v.Seats.filter((cc, ci) => {
          // console.log(v,_this.search.type)
          if (cc.Seat_name == _this.search_train.type) {
            // console.log(cc.Seat_name)
            return cc;
          }
          return false;
        });
        // console.log(v.Seats,canbislist)
        v.Seats = canbislist;
        // console.log( v.Seats)

        v.From_time2 = v.From_time.replace(":", "");
        let vb = v.Seats;
        v.allseats = [];
        let lowest = 10000000;
        vb.sort((a, b) => {
          return a.Seat_price - b.Seat_price;
        });

        vb.map((a, i) => {
          if (a.Seat_yupiao && a.Seat_price < lowest) {
            lowest = a.Seat_price;
            v.lowest = i;
          }
          if (all_s.indexOf(a.Seat_name) == -1) {
            all_s.push(a.Seat_name);
          }
          if (v.allseats.indexOf(a.Seat_name) == -1) {
            v.allseats.push(a.Seat_name);
          }
        });
        v.Lowest_price = lowest;
        // this.$set(v, "Use_time2", this.calc_time(v.Use_time));
        v.Use_time2 = this.calc_time(v.Use_time);
        // this.$set(
        //   v,
        //   "From_station_data",
        //   this.find_item_train(this.train_ku, v.From_station)
        // );
        v.From_station_data = this.find_item_train(this.train_ku, v.From_station);
        // this.$set(
        //   v,
        //   "To_station_data",
        //   this.find_item_train(this.train_ku, v.To_station)
        // );
        v.To_station_data = this.find_item_train(this.train_ku, v.To_station);
        // console.log(v);
      });
    },
    sort_text (x) {
      let a = ["价格", "出发", "航司"];
      let b = "";
      if (this.sort_list[3] === x) {
        b = !this.sort_list[4] ? "降序" : "升序";
      } else {
        b = "排序";
      }
      return a[x] + b;
    },
    sort_by (x) {
      let v = this.sort_list;
      if (this.sort_list[3] === x) {
        this.sort_list[4] = !this.sort_list[4];
      } else {
        this.sort_list[3] = x;
        this.sort_list[4] = true;
      }
      let _this = this;
      this.FlightList.sort((a, b) => {
        if (_this.sort_list[4]) {
          var v1 = a[_this.sort_list[x]];
          var v2 = b[_this.sort_list[x]];
        } else {
          var v2 = a[_this.sort_list[x]];
          var v1 = b[_this.sort_list[x]];
        }
        if (x == 0 || x == 1) {
          v1 = v1 * 1;
          v2 = v2 * 1;
          return v1 - v2;
        } else {
          return v1 > v2 ? "1" : "-1";
        }
      });
