    async check_psg (formData) {
      // this.upload();

      let that = this;
      await axios({
        method: "POST",
        url: this.apilistx[0] + "query12306PassengerInfo",
        dataType: "json", //预期的服务器响应的数据类型。
        data: JSON.stringify({
          account12306Info: {
            userName12306: formData.user,
            userPass12306: formData.pass,
            requestKey: new GUID().newGUID(),
          },
        }),
      }).then(
        (res) => {
          let e = res.data;
          var g = JSON.parse(sessionStorage.getItem("book_train"));
          if (e.length + g.list.length <= 15) {
            this.check_psg2(true, formData);
          } else {
            let num = e.length + g.list.length;

            e.map((v, i) => {
              g.list.map((v2, i2) => {
                if (
                  v.certificateName.replace(/\//g, " ") ==
                  v2.key.FltPaxnameDom.replace(/\//g, " ") &&
                  v.certificateNo.substr(-3) == v2.key.IdNo.substr(-3)
                ) {
                  num--;
                }
              });
            });
            if (num <= 15) {
              this.check_psg2(true, formData);
            } else {
              this.check_psg2(false, formData);
            }
          }
        },
        (error) => {
          // that.$toast.clear();
          console.log(error);
          that.messConfirm_ui("请求失败，请重新提交");
        }
      );
      // .catch((e) => {
      //   this.$toast.clear();
      //   that.messConfirm_ui("请求失败，请重新提交");
      // });
    },
    check_psg2 (e, formData) {
      let that = this;
      if (e) {
        // that.$toast.clear();
        let g = JSON.parse(sessionStorage.getItem("book_train"));

        let formd = formData;
        g.username12306 = formd.user;
        g.password12306 = formd.pass;

        g.list.map((v, i) => {
          //删除唯一id
          delete v.key.$id;
          let vi = v.items;
          vi.map(function (v2, i2) {
            delete v2.psg.$id;
          });
        });
        let allstr = JSON.stringify(g);
        let guidx = g.timespan;
        let window_width = window.innerWidth;
        let is_lth720 = window_width <= 720;
        console.log("通过");
        this.save12306(function () {
          try {
            if (that.book_train) {
              let allstr = JSON.stringify(g);
              console.log("12306", g);
              that.book_train(allstr);
            }
          } catch (e) {
            that.check_psg2(false);
            console.log(e);
          }
        }, formData);
      } else {
        this.v_s.status = -2;
        this.s12306ve4 = true; //乘车人超过15人
        // that.$toast.clear();
      }
    },
