    async book_train (allstr) {
      // this.upload();
      await axios({
        method: "POST", //规定请求的类型（GET 或 POST）。
        url: "https://porsche.efasco.com/ErpService.asmx/SavePorscheTrainTrips", //点击提交数据时,要发送的东西的路径
        contentType: "application/x-www-form-urlencoded;charset=UTF-8", //发送数据到服务器时所使用的内容类型,并注明使用的是UTF-8编码。默认是："application/x-www-form-urlencoded"。
