      })
        .then((res) => {
          // this.$toast.clear();
          this.Traveler_popup = false;
          this.pupRelevanceShow = false;

          var result = res.data;
          console.log(result, result.toString().indexOf("error"));
          if (result.toString().indexOf("error") < 0) {
            console.log(
              "向服务器发送数据成功后返回回来的信息变量result--->",
              result
            );
            allstr = JSON.stringify(result); //设置变量allstr的值 为 向服务器发送数据成功后返回回来的信息转换成的字符串;
            this.sussTime = result.tktdeadline;
            this.submt_info = result;
