      sessionStorage.setItem("submt_info", JSON.stringify(this.submt_info));
      // console.log(data);
      this.$router.push({
        path: "/applicationdetail",
        query: {
          StaffNo: data.staffno,
          orderid: data.timespan,
          Status2: 1,
          auth: 0,
          formorder: true,
        },
      });
      // console.log(data)
    },

    //差旅目的数据
    purposeItem (data) {
      // console.log(data);
      this.purposeValueObj = data;
    },
    paymenItem (data) {
      this.paymenValueObj = data;
      // console.log(this.paymenValueObj)
    },
    get_null_tru (item) {
      if (item == null) return "";
      else return item;
    },
    calc_time (time) {
      var hour = parseInt(time / 60);
      var min = time % 60;
      // console.log(hour,min)

      // return hour + "小时" + min + "分";
      return hour + "H" + min + "M";
    },
    find_item_train (json, item) {
      if (!!json[item]) {
        return json[item];
      } else {
        this.add_arr_train(json, item);
        return this.find_item_train(json, item);
      }
    },
    add_arr_train (json, item) {
      let city_json = JSON.parse(sessionStorage.getItem("city_train"));
      json[item] = {
        namecn: item,
        nameen: "",
      };
      for (let i = 0; i < city_json.length; i++) {
        if (city_json[i].namecn == item) {
          json[item] = city_json[i];

          return;
        }
      }
    },

    //展开列表
    // onOpen(index) {
    //   if (index == this.isOpen) {
    //     this.isOpen = -1;
    //   } else {
    //     this.isOpen = index;
    //   }
    // },
    onOpen (i) {
      if (this.$refs[i][0].style.display == "none") {
        this.$refs[i][0].style.display = "block";
      } else if (this.$refs[i][0].style.display == "block") {
        this.$refs[i][0].style.display = "none";
      }
    },

    // upload () {
    //   this.$toast.loading({
    //     message: "Loading...",
    //     duration: 0,
    //     forbidClick: true,
    //   });
    // },
    messConfirm_ui (mes) {
      showDialog({
        title: "提示/Notice",
        message: mes,
        theme: "round-button",
        confirmButtonText: "确定/Confirm", //改变确认按钮上显示的文字
        confirmButtonColor: "linear-gradient(160deg, #0274c9 0%, #044db5 100%)", //确定按钮文字
      });
    },

    getDay (a) {
      return new Date(a).getMonth() + 1 + "月" + new Date(a).getDate() + "日";
    },
    getWeek (a) {
      let v = ["天", "一", "二", "三", "四", "五", "六", "天"];
      return "周" + v[new Date(a).getDay()];
    },
    enMonth (e) {
      var monthsInEng = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Spt",
        "Oct",
        "Nov",
        "Dec",
      ]; //英文月份
      return monthsInEng[new Date(e).getMonth()] + " " + new Date(e).getDate();
    },
    enDay (e) {
      var weekInEng = [
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
        "Sunday",
      ]; //英文日期
      return weekInEng[new Date(e).getDay()];
    },
    toMoney (num) {
      num = parseFloat(num);
      //console.log(num)
      //num = num.toFixed(2);
      //console.log(num)
      num = num + "";

      //console.log(num)
      if (num.indexOf(".") > 0) {
        var x = num.split(".")[1];
        var y = num.split(".")[0];
        if (x.length >= 2) {
          return y + "." + x.substr(0, 2);
        } else if (x.length == 1) {
          return num + "0";
        } else if (x.length == 0) {
          return num + "00";
        }
      } else {
        return num + ".00"; //返回的是字符串23,245.12保留2位小数
      }
    },
  },
  components: {
    ScrollTop,
  },
};
</script>
