<template>
  <div class="page">
    <van-sticky>
      <div class="head-title">
        <van-icon name="arrow-left" size="23" @click="
            $router.push({
              path: '/index',
            })
          " />
        <div class="head-t-text">
          <span></span>
          <i style="font-weight: 600"><span>差旅申请表</span><span style="font-size: 14px"><br />Travel Request</span></i>
        </div>
      </div>
    </van-sticky>

    <div style="padding: 0 10px">
      <div class="form-nav-box">
        <van-tabs v-model:active="active" lazy-render animated swipeable>
          <van-tab v-for="(item, i) in $route.path == '/applicationform'
              ? tab_Request
              : tab_approve" :key="i">
            <template #title>
              <div class="nav-box-text">
                <span>{{ item.title }}</span>
                <span>{{ item.titleEn }}</span>
              </div>
            </template>
            <div class="nav-box">
              <div class="nav-box-xx">
                <i>
                  <span>订单号/Order No</span><br />
                  <span>
                    <input type="text" v-model="OrderNo" placeholder="搜索订单编号" />
                  </span>
                </i>
              </div>
              <div class="nav-box-xx">
                <i>
                  <span>旅客姓名/Traveler Name</span><br />
                  <span><input type="text" v-model="CltTravelerName" placeholder="搜索旅客姓名" /></span>
                </i>
                <span class="img-box" @click="search"><img src="../../assets/image/search.png" alt="" /></span>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
    <!-- No data to display -->
    <div style="padding: 0 10px 76px">
      <template v-for="(item, i) in filter_list" :key="i">
        <div class="form-deli-box">
          <div style="padding: 10px">
            <div class="form-d-1">
              <h5 v-if="item.Status2 == 1">未提交审批/Not Submitted</h5>
              <h5 v-else-if="item.Status2 == 2">已提交审批/Pending Approval</h5>
              <h5 v-else-if="item.Status2 == 3">{{ item.Status }}/Issued</h5>
              <h5 v-else-if="item.Status2 == 4">{{ item.Status }}/Canceled</h5>
              <div class="form-d-info">
                <i>
                  <span>订单号/Order No.:</span>
                  <span>{{ item.No }}</span>
                </i>
                <i>
                  <span>预定日期/Booking Date:</span>
                  <span>{{ item.Date.split(/\s|T/g)[0] }}</span>
                </i>
                <i>
                  <span>出行日期/Departure Date:</span>
                  <span>{{ item.startDate }}</span>
                </i>
                <i v-if="item.f1.length">
                  <span>航班/Air:</span>
                  <span>
                    <span>{{ item.f1[0].OrigRemark }}/{{
                        item.f1[0].OrigAirport
                      }}</span><br />
                    <span>{{ item.f1[0].DestRouteRemark }}/{{
                        item.f1[0].DestRouteAirport
                      }}</span>
                  </span>
                </i>
                <i v-if="item.t1.length">
                  <span>航班/Air:</span>
                  <span>
                    <span>{{ item.t1[0].OrigAirport }}/{{
                        item.t1[0].OrigAirport
                      }}</span><br />
                    <span>{{ item.t1[0].DestRouteAirport }}/{{
                        item.t1[0].DestRouteAirport
                      }}</span>
                  </span>
                </i>
                <i v-else-if="item.h1.length">
                  <span>入住酒店/Hotel:</span>
                  <span>
                    <span>{{ item.h1[0].hotel_name }}</span><br />
                  </span>
                </i>
              </div>
            </div>
            <div class="form-d-2">
              <div class="form-d-2-box">
                <i>
                  <span>
                    <b>
                      <img src="../../assets/image/flit.png" alt="" />
                    </b>
                    {{ item.f1.length }}
                  </span>
                </i>
                <i><span>
                    <b>
                      <img src="../../assets/image/hotelx.png" alt="" />
                    </b>
                    {{ item.h1.length }}
                  </span></i>
                <i>
                  <span>
                    <b>
                      <img src="../../assets/image/tripx.png" alt="" />
                    </b>
                    {{ item.t1.length }}
                  </span>
                </i>
                <div class="detail-btn" @click="
                    $route.path == 'applicationform'
                      ? $router.push({
                          path: '/applicationdetail',
                          query: {
                            StaffNo: StaffNo,
                            orderid: item.MnlOrderNo
                              ? item.MnlOrderNo
                              : item.ExorderList[0].SsoTimespan,
                            Status2: item.Status2,
                            auth: $route.path == '/applicationform' ? 0 : 1,
                            formorder: true,
                            active: active,
                          },
                        })
                      : $router.push({
                          path: '/applicationdetail',
                          query: {
                            StaffNo: StaffNo,
                            orderid: item.MnlOrderNo
                              ? item.MnlOrderNo
                              : item.ExorderList[0].SsoTimespan,
                            Status2: item.Status2,
                            auth: $route.path == '/applicationform' ? 0 : 1,
                            formorder: true,
                            active: active,
                          },
                        })
                  ">
                  详情/Detail
                </div>
              </div>
            </div>
          </div>
        </div>
      </template>
      <!-- <div v-if="!filter_list">暂无数据</div> -->
    </div>

    <!-- <div style="padding: 0 10px 76px">666</div> -->

    <!-- 底部工具 -->
    <div class="bottom-toolbars">
      <van-grid :border="false" :icon-size="22">
        <div class="btn-too-box">
          <van-grid-item @click="sort_by(0)" icon="after-sale" :text="sort_text(0)" />
          <span>Departure</span>
        </div>

        <div class="btn-too-box">
          <van-grid-item @click="sort_by(1)" icon="underway-o" :text="sort_text(1)" />
          <span>Booking</span>
        </div>

        <div class="btn-too-box">
          <van-grid-item @click="sort_by(2)" icon="notes-o" :text="sort_text(2)" />
          <span>Approval</span>
        </div>

        <!-- <div class="btn-too-box">
          <van-grid-item icon="filter-o" text="筛选" />
          <p>Filter</p>
        </div> -->
      </van-grid>
    </div>
    <!-- 底部工具结束 -->
  </div>
</template>
<script>
import axios from "../../utils/axiosCwellsapi";
import { showDialog } from 'vant';
export default {
  data () {
    let home_datax = sessionStorage.getItem("home_datax")
      ? JSON.parse(sessionStorage.getItem("home_datax"))
      : {};
    return {
      active: this.$route.query.active ? Number(this.$route.query.active) : 0,
      StaffNo: sessionStorage.getItem("StaffNo"),
      StaffNox: "RS_777-EC",
      // StaffNox: "SECOEC841",
      searchName: "",
      tab_Request: [
        { title: "未提交审批", titleEn: "Not Submitted" },
        { title: "已提交审批", titleEn: "Pending Approval" },
        { title: "已出票", titleEn: "Issued" },
        { title: "已取消", titleEn: "Canceled" },
      ],
      tab_approve: [
        { title: "待审批", titleEn: "Pendign Approval" },
        { title: "已审批", titleEn: "Approved" },
        { title: "已驳回", titleEn: "Reject" },
      ],
      order_num: "",
      traveller_name: "1",
      // tab_approve: [{}],
      quickSearch: [
        {
          startDate: home_datax.approveurl_parameter.start,
          endDate: home_datax.approveurl_parameter.end,
        },
        {
          startDate: home_datax.approveurl_parameter.start,
          endDate: home_datax.approveurl_parameter.end,
        },
      ],
      OrderNo: "",
      CltTravelerName: "",
      Searchlist: [[], []],
      pathx: this.$route.path,
      ListInfo: [],
      Searchlistx: [],

      sort_list: ["startDateX", "DateX", "FreecancelDeadline", null, null],
    };
  },
  created () {
    this.cache_data();
    // console.log(this.filter_list)
    // this.search_list();
    // console.log()
  },
  mounted () {
    // this.upload();
  },
  methods: {
    sort_text (x) {
      let a = ["出发", "预定", "审批"];
      let b = "";
      if (this.sort_list[3] === x) {
        b = !this.sort_list[4] ? "降序" : "升序";
      } else {
        if (x == 0) {
          b = "日期";
        }
        if (x == 1) {
          b = "日期";
        }
        if (x == 2) {
          b = "时限";
        }
      }

      return a[x] + b;
    },
    sort_by (x) {
      // var v = this.sort_list;
      var path = this.$route.path;

      var n = path == "/applicationform" ? 0 : 1;

      // var t = this.Searchlist[n];

      if (this.sort_list[3] === x) {
        this.sort_list[4] = !this.sort_list[4];
      } else {
        this.sort_list[3] = x;
        this.sort_list[4] = true;
      }

      let _this = this;
      this.Searchlist[n].sort((a, b) => {
        //sort 数组排序
        // Date.split(/\s|T/g)[0]
        if (_this.sort_list[4]) {
          var v1 = a[_this.sort_list[x]];
          var v2 = b[_this.sort_list[x]];
        } else {
          var v2 = a[_this.sort_list[x]];
          var v1 = b[_this.sort_list[x]];
        }
        if (x == 0 || x == 1 || x == 2) {
          v1 = v1 * 1;
          v2 = v2 * 1;
          return v1 - v2;
        }
        // } else {
        //   return v1 > v2 ? "1" : "-1";
        // }
      });
      // this.TicketList.sort((a, b) => {
      //   //sort 数组排序
      //   if (_this.sort_list[4]) {
      //     var v1 = a.segments[0][_this.sort_list[x]];
      //     var v2 = b.segments[0][_this.sort_list[x]];
      //   } else {
      //     var v2 = a.segments[0][_this.sort_list[x]];
      //     var v1 = b.segments[0][_this.sort_list[x]];
      //   }
      //   if (x == 0 || x == 1) {
      //     v1 = v1 * 1;
      //     v2 = v2 * 1;
      //     return v1 - v2;
      //   } else {
      //     return v1 > v2 ? "1" : "-1";
      //   }
      // });
    },
    upload () {
      showDialog({
        message: "loading...",
        duration: 0,
        forbidClick: true,
      });
    },

    cache_data () {
      let path = this.$route.path;
      //   console.log(path);
      let f = sessionStorage.getItem(path);

      if (f) {
        f = JSON.parse(f);
        this.deal_data(f);
        // this.$set(this.Searchlist, path == "/applicationform" ? 0 : 1, f);
        const index = path === '/applicationform' ? 0 : 1
        this.Searchlist[index] = f
        // this.get_data(true);
        this.get_data_x(true);
      } else {
        // this.get_data();
        this.get_data_x(true);
      }
    },
    async get_data_x (a) {
      let _this = this;
      let path = this.$route.path;
      if (!a) {
        // this.upload();
      }
      let paxname = "";
      let orderno = "";
      let myDate = new Date();
      let start = new Date(myDate.getTime() - 1000 * 60 * 60 * 24 * 360).format(
        "yyyy-MM-dd"
      );
      let lw = new Date(myDate.getTime() - 1000 * 60 * 60 * 24 * 30).format(
        "yyyy-MM-dd"
      );
      let end = new Date(
        new Date().getFullYear(),
        new Date().getMonth(),
        new Date().getDate() + 1
      ).format("yyyy-MM-dd");

      let url =
        path == "/applicationform"
          ? "https://timas.efasco.com/ErpService.asmx/GetStaffTrips?staffno=" +
          this.StaffNo +
          "&start=" +
          lw +
          // this.quickSearch[0].startDate +
          "&end=" +
          this.quickSearch[0].endDate
          : "https://ezway.efasco.com/ErpService.asmx/GetNeedAuthTripsByNo?staffno=" +
          this.StaffNo +
          "&start=" +
          lw +
          "&end=" +
          end +
          "&paxname=" +
          paxname +
          "&orderno=" +
          orderno;

      let f = await axios.get(url);

      console.log(f); //
      if (!a) {
        // this.$store.commit('hideLoading')
      }
      //  sessionStorage.setItem(path, JSON.stringify(f.data));
      // this.deal_data(f.data);
      // this.$set(this.Searchlist, path == "/applicationform" ? 0 : 1, f.data);
      // //   console.log(this.Searchilist)

      // if (this.pathx == "/applicationform") {
      //   this.ListInfo = this.Searchlist[0];
      // } else {
      //   this.ListInfo = this.Searchlist[1];
      // }

      if (f.data.length > 0) {
        sessionStorage.setItem(path, JSON.stringify(f.data));
        this.deal_data(f.data);
        // this.$set(this.Searchlist, path == "/applicationform" ? 0 : 1, f.data);
        const index = path === "/applicationform" ? 0 : 1
        this.Searchlist[index] = f.data
        //   console.log(this.Searchilist)

        if (this.pathx == "/applicationform") {
          this.ListInfo = this.Searchlist[0];
        } else {
          this.ListInfo = this.Searchlist[1];
        }
      } else {
        showDialog({
          title: "提示/Notice",
          message: "无数据显示/No data to display",
          theme: "round-button",
          confirmButtonText: "确定/Confirm", //改变确认按钮上显示的文字
          confirmButtonColor:
            "linear-gradient(160deg, #0274c9 0%, #044db5 100%)", //确定按钮文字
        });
      }

      // console.log(this.ListInfo)
    },

    async search_list (a) {
      let myDate = new Date();
      let lw = new Date(myDate.getTime() - 1000 * 60 * 60 * 24 * 30).format(
        "yyyy-MM-dd"
      );
      let data = {
        startdate: lw,
        enddate: new Date(
          new Date().getFullYear(),
          new Date().getMonth(),
          new Date().getDate() + 1
        ).format("yyyy-MM-dd"),
      };
      if (typeof data.startdate == "object") {
        data.startdate = data.startdate.format("yyyy-MM-dd");
      }
      if (typeof data.enddate == "object") {
        data.enddate = data.enddate.format("yyyy-MM-dd");
      }
      console.log("查询", data);
      axios({
        method: "GET",
        url:
          "https://timas.efasco.com/ErpService.asmx/GetStaffTrips?staffno=" +
          this.StaffNo +
          "&start=" +
          data.startdate +
          "&end=" +
          data.enddate,
      }).then((res) => {
        let resultx = res.data;
        sessionStorage.setItem("pathx", JSON.stringify(resultx));
        this.Searchlistx = resultx;
        this.deal_datax(resultx);
        // for (let j = resultx.length - 1; j >= 0; j--) {
        //   // if (resultx[j].Status == "已出票") {
        //   //   resultx[j].Status ='已审批'
        //   // }
        //   console.log(resultx[j])
        // }
      });
    },
    deal_datax (data) { },
    // async get_data(a) {
    //   let path = this.$route.path;
    //   if (!a) this.upload();
    //   console.log();

    //   let url =
    //     path == "/applicationform"
    //       ? "https://cwellsapi.casonline.cn/apiservice/apiservice.asmx/GetStaffTrips?staffno=" +
    //         this.StaffNo +
    //         "&start=" +
    //         this.quickSearch[0].startDate +
    //         "&end=" +
    //         this.quickSearch[0].endDate
    //       : // ?"https://timas.efasco.com/ErpService.asmx/GetStaffTrips?staffno=" +
    //         // this.StaffNox +
    //         // "&start=" +
    //         // this.quickSearch[0].startDate +
    //         // "&end=" +
    //         // this.quickSearch[0].endDate
    //         "https://cwellsapi.casonline.cn/apiservice/apiservice.asmx/GetNeedAuthTripsByNo?staffno=" +
    //         this.StaffNo +
    //         "&start=" +
    //         this.quickSearch[1].startDate +
    //         "&end=" +
    //         this.quickSearch[1].endDate +
    //         "&paxname=" +
    //         "" +
    //         "&orderno=" +
    //         "";
    //   let f = await this.$axios.get(url);
    //   console.log(f); //
    //   if (!a) this.$toast.clear();
    //   sessionStorage.setItem(path, JSON.stringify(f.data));
    //   this.deal_data(f.data);
    //   this.$set(this.Searchlist, path == "/applicationform" ? 0 : 1, f.data);
    //   //   console.log(this.Searchilist)

    //   if (this.pathx == "/applicationform") {
    //     this.ListInfo = this.Searchlist[0];
    //   } else {
    //     this.ListInfo = this.Searchlist[1];
    //   }
    //   // console.log(this.ListInfo)
    // },
    deal_data (data) {
      //2021-07-30 17:59:00
      // console.log(data)
      let sf = [[], []];
      let n = this.$route.path == "/applicationform" ? 0 : 1;
      data.map((v, i) => {
        let exo = v.ExorderList;
        let all_cancel = true;
        let au_dl = "";
        let f1 = (v.f1 = []);
        let h1 = (v.h1 = []);
        let t1 = (v.t1 = []);
        let all_trip_num = 0;
        var ndate = v.Date.replace(/-/g, "/");

        // console.log(exo)
        exo.map((v2) => {
          // console.log(v2.OrderStatus)
          // var Freed = v2.FreecancelDeadline.split(/[- : \/]/);
          // var Freed2=Date.parse(Freed[0],Freed[1],Freed[2],Freed[3],Freed[4],Freed[5]);
          // console.log(Freed2)
          var Freed = Date.parse(v2.FreecancelDeadline);
          // console.log(Freed)
          v.FreecancelDeadline = Freed; //审批时限筛选数值

          var dd = v.Date.split(/\s|T/g)[0]; //数组化取的下标0的数据
          var datax = Date.parse(dd);

          v.DateX = datax; //预定时间筛选数值

          if (sf[1].indexOf(v2.OrderStatus) == -1) sf[1].push(v2.OrderStatus);
          all_trip_num++;
          if (v2.ProductCat.toLowerCase() == "f") {
            if (all_trip_num == 1) {
              v.startDate = v2.DepartureDate.split(/\s|T/g)[0];
            }
            f1.push(v2);
          }
          if (v2.ProductCat.toLowerCase() == "h") {
            let ht = v2.OrderDescr;
            let ht1 = ht.split("酒店名称:")[1];
            let ht2 = ht1.split("Check In(入住):");
            let ht2_1 = ht2[0].replace(/\s/g, "");
            let ht2_2 = ht2[1].split("Check Out(离店):");
            let ht2_2_1 = ht2_2[0].replace(/\s/g, "");
            let ht2_2_2 = ht2_2[1].replace(/\s/g, "");
            v2.hotel_checkin = ht2_2_1;
            v2.hotel_checkout = ht2_2_2;

            v2.hotel_name = ht2_1;
            h1.push(v2);
            if (all_trip_num == 1) {
              v.startDate = v2.hotel_checkin;
            }
          }
          if (v2.ProductCat.toLowerCase() == "t") {
            t1.push(v2);
            if (all_trip_num == 1) {
              v.startDate = v2.DepartureDate.split(/\s|T/g)[0];
            }
          }
          if (v2.FreecancelDeadline) {
            let tf = new Date(
              v2.FreecancelDeadline.replace(/-/g, "/")
            ).getTime();
            if (!au_dl) au_dl = tf;
            if (tf < au_dl) au_dl = tf;
          }
          if (!v.Status) v.Status = v2.OrderStatus;
          if (v2.OrderStatus != "已取消") all_cancel = false;

          if (!all_cancel && v.Status == "已取消") {
            //v.exoList里的OrderStatus不等于已取消、v.Status等于已取消时 重新赋值
            v.Status = "未提交审批";
          }
          var startDateX = Date.parse(v.startDate);
          // console.log(startDateX)

          v.startDateX = startDateX;

          if (!v.Status && v2.OrderStatus != "未确认") {
            v.Status = v2.OrderStatus;
          }
          if (v2.OrderStatus == "已取消" && v.Status && v.Status != "已取消") {
            v.Status = v2.OrderStatus;
          }

          // var all_ancel = true;

          // if (v2.OrderStatus != "已取消") all_ancel = false;
        });
        v.au_dl = au_dl;
        v.ndate2 = v.No.split("-")[1];
        v.ndate = ndate;
        if (sf[0].indexOf(v.Status) == -1) sf[0].push(v.Status);
        // console.log(v.Status);

        let x = v.Status;
        // let xE=v.Status.ExorderList
        // console.log(v);
        if (x == "已出票" || x == "已审批" || x == "已确认" || x == "已支付") {
          v.Status2 = 3;
        } else if (
          x == "未提交审批" ||
          x == "未确认" ||
          x == "申请改期_未确认"
        ) {
          v.Status2 = 1;
        } else if (x == "待审批" || x == "已提交审批" || x == "待确认") {
          v.Status2 = 2;
        } else if (x == "已取消" || x == "已退票") {
          v.Status2 = 4;
        }
      });
      console.log(sf);
      //   this.Searchlist[0].map((v)=>{
      //           console.log(v.h1)
      //   })
    },
    search () {
      let path = this.$route.path;

      let n = path == "/applicationform" ? 0 : 1;
      let s = path == "/applicationform" ? 1 : 2;

      let t = this.Searchlist[n];
      t = this.search_data;
      t.filter((v) => {
        return false;
      });
      // let a = this.search_data.filter((v) => {

      // });
    },
  },
  computed: {
    filter_list () {
      let status = [["all", ""]];
      // this.$store.commit('showLoading')
      let path = this.$route.path;
      let n = path == "/applicationform" ? 0 : 1;
      let s = path == "/applicationform" ? 1 : 2;

      let t = this.Searchlist[n];

      //
      // if (this.Searchlist[n].length > 0) {
      //   this.OrderNo = this.Searchlist[n][0].No;
      //   this.CltTravelerName =
      //     this.Searchlist[n][n].ExorderList[0].CltTravelerName;
      // }

      // console.log(this.OrderNo)
      // this.$store.commit('hideLoading')
      let a = t.filter((v) => {
        let s2 = v.Status2 - s;
        // console.log(this.active, v.Status2);
        // if (this.active == 0) return s2;
        // this.$store.commit('hideLoading')
        return s2 == this.active;
      });

      // console.log(a)
      this.search_data = a;
      // return a;

      return a.filter((v) => {
        // return v
        // this.$store.commit('hideLoading')
        if (!this.CltTravelerName && !this.OrderNo) return v;
