<template>
  <div class="travel_set">
    <div>
      <div class="travel_set_item" v-for="(item, i) in travel_set2" :key="i" v-show="!item.hide">
        <h4>{{ item.title }}</h4>
        <van-field class="show_border" v-model="item.value" :clickable="item.columns.length != 0" :readonly="item.columns.length != 0" @click="
            item.ctr = true;
            active = i;
          " />
        <van-popup v-if="item.columns.length > 0" class="select_popup" v-model:show="item.ctr" round position="bottom">
          <van-search v-if="!item.not_need_search" v-model="item.valuesearch" :placeholder="'查詢'" />
          <van-picker show-toolbar allow-html :columns="get_this_list(item, i)" @confirm="choose_me" @cancel="item.ctr = false" @change="onChange_me" />
        </van-popup>
      </div>
    </div>
    <div class="pup"></div>
  </div>
</template>
<script>
export default {
  props: {
    travel_set2: {
      type: Array,
      default: () => [],
    },
  },
  data () {
    return {
      active: null,
      //   showtp: false,
      //   showrc: false,
      //   rclist: [1, 2, 2, 3, 4, 5],
      //   tplist: ["因公", "因私"],
      //   boker: JSON.parse(sessionStorage.getItem("booker")),
    };
  },
  computed: {},
  created () {
    // console.log(this.travel_set2);
  },
  methods: {
    filterx (arr, str) {
      return arr.filter((v) => {
        return (v + "").indexOf(str) != -1;
      });
    },
    choose_me (v) {
      console.log('v', v);
      if (typeof v == "object") {

        v = [v.selectedOptions[0].text, v.selectedOptions[1].text];
        console.log(v);
      }
      this.travel_set2[this.active].value = v;
      this.travel_set2[this.active].ctr = false;

      this.$emit("choose_travel_set2", this.travel_set2[this.active]);
    },
    onChange_me (value) {
      // console.log(value);
    },
    get_this_list (item) {
      // console.log('item', item);
      let a = [];

      if (item.to) {
        a = item.columns.map((v) => v[item.to]);
      }
      if (item.not_need_search) return item.columns;
      return this.filterx(item.to ? a : item.columns, item.valuesearch);
    },
  },
};
</script>
<style scoped >
.travel_set h4 {
  color: #a4a4a4;
  font-size: 13px;
  font-weight: normal;
  padding: 5px 0;
}
.show_border {
  border-bottom: 1px solid #97979738;
  padding: 5px 0px;
}
</style>