import CryptoJS from "crypto-js"
let appkey = "5c8835cad74471e0d4030a48b03193ac",
  appkeyx = appkey.substr(appkey.length - 8),
  keyHex = CryptoJS.enc.Utf8.parse(appkeyx),
  ivHex = CryptoJS.enc.Utf8.parse(appkeyx)
export default {
  //加密
  encrypt_up(word) {
    //账号密码
    console.log(word)
    var key = CryptoJS.enc.Utf8.parse("abcdefgabcdefg12")
    var srcs = CryptoJS.enc.Utf8.parse(word)
    var encrypted = CryptoJS.AES.encrypt(srcs, key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    })

    return encrypted.toString()
  },
  encrypt(word, keyStr) {
    keyStr = keyStr ? keyStr : "smartmice2810210"
    var key = CryptoJS.enc.Utf8.parse(keyStr) //Latin1 w8m31+Yy/Nw6thPsMpO5fg==
    var srcs = CryptoJS.enc.Utf8.parse(word)
    var encrypted = CryptoJS.AES.encrypt(srcs, key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    })
    return encrypted.toString()
  },
  //解密
  // decrypt(word, keyStr){
  //   // console.log(word)
  //   keyStr = keyStr ? keyStr : 'smartmice2810210';
  //   var key  = CryptoJS.enc.Utf8.parse(keyStr);//Latin1 w8m31+Yy/Nw6thPsMpO5fg==
  //   var decrypt = CryptoJS.AES.decrypt(word, key, {mode:CryptoJS.mode.ECB,padding: CryptoJS.pad.Pkcs7});
  //   return CryptoJS.enc.Utf8.stringify(decrypt).toString();
  // },
  decrypt(word) {
    if (!word) return word
    // console.log(word)

    word = word //.replace(/t-\^/g,'=').replace(/b-\^/g,'&')
    var key = CryptoJS.enc.Utf8.parse("abcdefgabcdefg12")
    var decrypt = CryptoJS.AES.decrypt(word, key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    })
    return CryptoJS.enc.Utf8.stringify(decrypt).toString()
  },
  decryptByDES(message, key) {
    var decrypt = CryptoJS.DES.decrypt(message, CryptoJS.enc.Utf8.parse(key), {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    }).toString(CryptoJS.enc.Utf8)
    return decrypt
  },
  encryptByDES2(message) {
    var encrypted = CryptoJS.DES.encrypt(message, keyHex, {
      iv: ivHex,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7,
    })
    var ciphertext = encrypted.ciphertext.toString()
    return ciphertext
  },
  decryptByDES2(message, key) {
    var keyHex = CryptoJS.enc.Utf8.parse(key)
    var encrypted = CryptoJS.DES.encrypt(message, keyHex, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    })
    return encrypted.toString()
  },
}
