<template>
  <Loading v-show="LOADING"></Loading>
  <router-view />
</template>
<script>
import { mapState } from "vuex";
import Loading from "./components/loadingPage/loading.vue";
import store from './store';
export default {
  name: 'App',
  components: { Loading },
  computed: {
    ...mapState(['LOADING'])
  },
  watch: {

  },
  created () {
    if (!this.$store.state.CltId_set.Cltid && CltId_set.CltId) {
      store.commit("CltId_set", CltId_set)
    }
  },
  mounted () {
    // 根据不同的设备使用不同的页面
    this.getScreen()
  },
  methods: {
    getScreen () {
      if (navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
      ) {
        this.$store.commit('MobileApp')
      } else {
        this.$store.commit('MobilePc')
      }
    }
  }
}
</script>
