<template>
  <div class="map">
    <header class="head" style="position: relative">
      <span></span>
      <span>{{ listData[0].title }}</span>
      <span @click="mapClose"><van-icon name="cross" style="position: absolute; right: 16px; top: 6px" size="18" /></span>
    </header>
    <div id="map_baidu">
      <baidu-map :center="center" :zoom="zoom" class="bm-view" ak="lEQGVRVKR8nmCKsohO20tmabKHsf8QSX" v-if="mapShow" @ready="handler">
        <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
        <bm-marker :position="center" :dragging="false" @click="infoWindow.show = true">
          <bm-info-window :title="listData[0].title" :show="infoWindow.show" @close="infoWindowClose" @open="infoWindowOpen">
            <p v-text="'地址: ' + listData[0].data.Address"></p>
          </bm-info-window>
        </bm-marker>
      </baidu-map>
    </div>
  </div>
</template>
<script>
import { BaiduMap } from 'vue-baidu-map-3x'
import { BmNavigation } from "vue-baidu-map-3x";
import { BmInfoWindow } from "vue-baidu-map-3x";
import { BmMarker } from "vue-baidu-map-3x";
export default {
  props: {
    listData: {
      type: Array,
      default: () => [],
    },
    showMap: {
      type: Boolean,
      default: true,
    },
  },
  data () {
    return {
      center: { lng: 0, lat: 0 },
      zoom: 3,
      infoWindow: {
        show: true,
      },
      mapShow: true,
      mapCloseValue: false,
    };
  },
  created () {
    // console.log(this.listData);
    this.mapShow = true;
  },
  watch: {
    showMap: {
      handler (e) {
        if (e == true) {
          this.mapShow = false;

          this.$nextTick(() => {
            this.mapShow = true;
            this.infoWindow.show = true;
          });
        }
      },
      deep: true,
    },
  },

  methods: {
    handler ({ BMap, map }) {
      const mapD = this.listData[0];
      this.center.lng = mapD.BaiduLon;
      this.center.lat = mapD.BaiduLat;
      this.zoom = 20;
      console.log(BMap, map, mapD);
    },
    infoWindowClose (e) {
      this.infoWindow.show = false;
    },
    infoWindowOpen (e) {
      this.infoWindow.show = true;
    },
    mapClose () {
      this.$emit("mapCloseValue", this.mapCloseValue);
    },
  },
  components: {
    BaiduMap,
    BmNavigation,
    BmInfoWindow,
    BmMarker,
  },
};
</script>
<style lang="scss" scoped>
.map {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  display: flex;
  flex-direction: column;

  // background-color: white;
  .head {
    line-height: 30px;
    font-size: 16px;
    color: #fff;
    // background-color: #403f45;
    background: linear-gradient(to bottom, #2c87ce, #2062b3);
    padding: 0 2%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    display: flex;
    justify-content: space-between;
  }
  #map_baidu {
    flex-grow: 1;
    padding: 3%;
    background-color: white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;

    .bm-view {
      width: 100%;
      height: 100%;
    }
  }
}
</style>