// 加密
import CryptoJS from "crypto-js"
export default {
  // 对账号进行加密
  encrypt_up(word) {
    //账号密码
    console.log(word)
    var key = CryptoJS.enc.Utf8.parse("abcdefgabcdefg12")
    var srcs = CryptoJS.enc.Utf8.parse(word)
    var encrypted = CryptoJS.AES.encrypt(srcs, key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    })

    return encrypted.toString()
  },
  // 解密    data：要加密解密的数据    AES_KEY: 秘钥   IV：偏移量
  decrypt(data) {
    if (!data) return ""
    data = data ? data + "" : ""
    // 秘钥
    const AES_KEY = CryptoJS.enc.Utf8.parse("abcdefgabcdefg12")
    // 数据
    const decrypt = CryptoJS.AES.decrypt(data, AES_KEY, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    }).toString(CryptoJS.enc.Utf8)
    return decrypt
  },
  // 加密
  encrypt(data) {
    const AES_KEY = CryptoJS.enc.Utf8.parse("abcdefgabcdefg12")
    const srcs = CryptoJS.enc.Utf8.parse(data)
    const encrypted = CryptoJS.AES.encrypt(srcs, AES_KEY, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    })
    return encrypted.toString()
  },
  // 对StaffNo 进行加密
  encryptStaffno(word) {
    const key = CryptoJS.enc.Utf8.parse("abcdefgabcdefg12")
    const srcs = CryptoJS.enc.Utf8.parse(word)
    const encryptStaff = CryptoJS.AES.encrypt(srcs, key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    })
    return encryptStaff.toString().replace(/=/g, "t-^").replace(/&/g, "b-^")
  },
}
