<template>
  <div class="page" :class="$route.query.auth == 0 ? 'application_detail' : 'approval_detail'">
    <van-sticky>
      <header class="head">
        <div class="head-title">
          <van-icon name="arrow-left" size="23" @click="
              $route.query.auth == 1
                ? $router.push({
                    path: '/approveform',
                    query: {
                      active: $route.query.active,
                    },
                  })
                : $router.push({
                    path: '/applicationform',
                    query: {
                      active: $route.query.active,
                    },
                  })
            " />
          <div class="head-title-txt" v-if="$route.query.auth == 0">
            <span>申请单</span><br />
            <span>Requests</span>
          </div>
          <div v-else class="head-title-txt">
            <!-- <img src="" /> -->
            <span>审批单</span><br />
            <span>Approval</span>
          </div>
        </div>
      </header>
    </van-sticky>

    <!-- 进度 -->
    <div style="padding: 0 10px" v-if="$route.query.auth == 0">
      <div class="bor_r" style="background-color: white" v-if="$route.path == '/applicationdetail'">
        <div style="height: 100%; padding: 0 8px">
          <div style="padding: 0 5px">
            <h5 class="steps_title" v-if="trip_res_show">
              申请单/Travel Request {{ trip_res.eorder_no }}
            </h5>
            <div class="steps_box">
              <template v-for="(item, i) in stepList" :key="i">
                <span class="steps_box_c" v-if="item.show">
                  <div class="line_box">
                    <span class="line" />
                  </div>
                  <div class="steps_item">
                    <div class="ico_box">
                      <div class="ico" :class="item.finish ? 'icoAcit' : 'icoIn'">
                        <img v-if="item.finish" :src="item.icon" alt="" />
                        <span v-else class="span_text">{{ item.id }}</span>

                        <div class="ico_text" style="font-size: 6px">
                          <span>{{ item.title }}<br />{{ item.titleEn }}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </span>
              </template>
            </div>
          </div>

          <span style="
              border-bottom: 1px solid #e0e0e0;
              height: 1px;
              width: 100%;
              display: inline-block;
              position: relative;
              bottom: -30px;
            "></span>
        </div>
      </div>
    </div>
    <!-- 进度 over-->

    <!-- 交通box -->
    <div class="sheet-box">
      <div class="sheet-bor" :class="$route.query.auth == 1 ? 'sheet-bor-rad' : ''">
        <h5>交通/Ttransportation</h5>
        <!-- 国内交通 -->
        <div class="sheet_frame" v-if="domesticData.length != 0">
          <h5>
            国内交通/Domestic Air
            <span class="add" v-if="$route.query.auth == 0 && status == 1" @click="goPath('/flight')"><van-icon name="plus" size="15" />新增/Add</span>
          </h5>
          <article v-for="(i, index) in domesticData" :key="index">
            <div>
              <span class="name_title"><van-icon size="19" :name="require('../../assets/image/name-icon.png')" />{{ i.name }}</span>
              <div class="outer_shell" v-for="(data, index) in i.infoData" :key="index">
                <i class="flightno_title">
                  <span v-if="data.flightno" class="flightno_img">
                    <img v-if="data.flightno" :src="
                        require('../../assets/image/fli/' +
                          data.flightno.substr(0, 2) +
                          '.png')
                      " alt="" />
                  </span>
                  <span style="color: rgb(235, 99, 1); margin: 0 5px 0 0">{{ data.flightno }}
                  </span>
                  <span>{{
                    calc_time2(
                      data.arrdate.replace(/-/g, "/") + " " + data.deptime,
                      data.arrdate.replace(/-/g, "/") + " " + data.arrtime
                    )
                  }}</span>
                </i>
                <div class="flight_info">
                  <i>
                    <p>
                      <span>{{ data.arrdate }}</span><br />
                      <span>{{ data.deptime }}</span>
                    </p>
                    <p>
                      <span>{{ data.depdate }}</span><br />
                      <span>{{ data.arrtime }}</span>
                    </p>
                  </i>
                  <i class="line"></i>
                  <i>
                    <p>
                      <span>{{ data.depairpot }}</span><br />
                      <span>{{ data.depairpot_en }}</span>
                    </p>
                    <p>
                      <span>{{ data.arrairpot }}</span><br />
                      <span>{{ data.arrairpot_en }}</span>
                    </p>
                  </i>
                </div>
                <div class="flight_detils">
                  <p class="flight_d_span flight_d_s_1">
                    <span>成本中心/Cost Center</span>
                    <span>{{ data.cccode }}/{{ data.cccode2 }}</span>
                  </p>
                  <p class="flight_d_span flight_d_s_2">
                    <span>出差目的/Travel Purpose</span>
                    <span>{{ data.travel_purpose }}/{{
                        getTravel_purpose_en(data.travel_purpose)
                      }}</span>
                  </p>
                  <p class="flight_d_span flight_d_s_3">
                    <span>{{ data.cabinlevel }}/{{ data.cabinlevel_en }}</span>
                    <span class="price">￥{{ toMoney(data.price * 1 + data.tax * 1) }}</span>
                  </p>
                  <p class="flight_d_span flight_d_s_3">
                    <span>票号/Ticket No:</span>
                    <span v-if="data.ticketno">{{ data.ticketno }}</span>
                    <span v-else class="last"><b>-</b></span>
                  </p>
                  <p class="flight_d_span flight_d_s_4">
                    <span>违反/Violate</span>
                    <span class="last"><b>{{ data.rccode }} </b>

                      <span v-if="data.rccode.length == 1"></span>
                      <span v-else @click="
                          $notify({
                            message: data.dtRcRemark,
                          })
                        ">
                        查看更多/Show More</span></span>
                  </p>
                </div>
              </div>
            </div>
          </article>
          <div class="changing">
            <span class="chang" v-if="trip_res.list[0].status == '已出票'">退票/Refund</span>
            <span class="chang" v-if="
                $route.query.auth == 0 &&
                  status == 3 &&
                  internationalData.length == 0 &&
                  (trip_res.list[0].status == '已出票' ||
                    trip_res.list[0].status == '被改签')
              " @click="ticketChange(trip_res)">改期/Reschedule</span>
          </div>
        </div>
        <!-- 国内交通 Over -->

        <!-- 国际交通 -->
        <div class="sheet_frame" v-if="
            trainData.length != 0 ||
              domesticData.length != 0 ||
              internationalData.length != 0 ||
              hotelData.length != 0
          ">
          <h5>
            国际交通/Internation Air
            <span class="add" v-if="$route.query.auth == 0 && status == 1" @click="goPath('/internationalflight')"><van-icon name="plus" size="14" />新增/Add</span>
          </h5>
          <template v-if="internationalData.length != 0">
            <article v-for="(i, index) in internationalData" :key="index">
              <span class="name_title"><van-icon size="19" :name="require('../../assets/image/name-icon.png')" />{{ i.name }}</span>
              <div class="outer_shell" v-for="(data, index) in i.infoData" :key="index">
                <i class="flightno_title">
                  <span v-if="data.flightno" class="flightno_img">
                    <img v-if="data.flightno" :src="
                      require('../../assets/image/fli/' +
                        data.flightno.substr(0, 2) +
                        '.png')
                    " alt="" />
                  </span>
                  <span style="color: rgb(235, 99, 1); margin: 0 5px 0 0">{{ data.flightno }} }</span>
                  <span>
                    {{
                    calc_time2(
                      data.arrdate.replace(/-/g, "/") + " " + data.deptime,
                      data.arrdate.replace(/-/g, "/") + " " + data.arrtime
                    )
                  }}
                  </span>
                </i>
                <div class="flight_info">
                  <i>
                    <p>
                      <span>{{ data.arrdate }}</span><br />
                      <span>{{ data.deptime }}</span>
                    </p>
                    <p>
                      <span>{{ data.depdate }}</span><br />
                      <span>{{ data.arrtime }}</span>
                    </p>
                  </i>
                  <i class="line"></i>
                  <i>
                    <p>
                      <span>{{ data.depairpot }}</span><br />
                      <span>{{ data.depairpot_en }}</span>
                    </p>
                    <p>
                      <span>{{ data.arrairpot }}</span><br />
                      <span>{{ data.arrairpot_en }}</span>
                    </p>
                  </i>
                </div>
                <div class="flight_detils">
                  <p class="flight_d_span flight_d_s_1">
                    <span>成本中心/Cost Center</span>
                    <span>{{ data.cccode }}/Marketing</span>
                  </p>
                  <p class="flight_d_span flight_d_s_2">
                    <span>出差目的/Travel Purpose</span>
                    <span>{{ data.travel_purpose }}/{{
                      getTravel_purpose_en(data.travel_purpose)
                    }}</span>
                  </p>
                  <p class="flight_d_span flight_d_s_3">
                    <span>票号/Ticket No:</span>
                    <span v-if="data.ticketno">{{ data.ticketno }}</span>
                    <span v-else class="last"><b>-</b></span>
                  </p>
                  <p class="flight_d_span flight_d_s_3">
                    <span>{{ data.cabinlevel }}/{{ data.cabinlevel_en }}</span>
                    <span class="price">￥{{ toMoney(data.price * 1 + data.tax * 1) }}</span>
                  </p>
                </div>
              </div>
            </article>
          </template>
          <div class="sheet_frame" v-else>
            <span class="not_data">No data to display</span>
          </div>

        </div>
        <!-- <div class="sheet_frame" v-else>
            <span class="not_data">No data to display</span>
          </div> -->

        <!-- 国际交通 Over -->

        <!-- 火车交通 -->
        <div class="sheet_frame" v-if="
            trainData.length != 0 ||
              domesticData.length != 0 ||
              internationalData.length != 0 ||
              hotelData.length != 0
          ">
          <h5>
            火车/Train
            <span class="add" v-if="$route.query.auth == 0 && status == 1" @click="goPath('/train')"><van-icon name="plus" size="14" />新增/Add</span>
          </h5>
          <template v-if="trainData.length != 0"></template>
          <article v-for="(i, index) in trainData" :key="index">
            <span class="name_title"><van-icon size="19" :name="require('../../assets/image/name-icon.png')" />{{ i.name }}</span>
            <div class="outer_shell" v-for="(data, index) in i.infoData" :key="index">
              <i class="flightno_title">
                <span style="color: rgb(235, 99, 1); margin: 0 5px 0 0">{{ data.flightno }}
                </span>
                <span>
                  {{
                    calc_time2(
                      data.arrdate.replace(/-/g, "/") + " " + data.deptime,
                      data.arrdate.replace(/-/g, "/") + " " + data.arrtime
                    )
                  }}
                </span>
              </i>
              <div class="flight_info">
                <i>
                  <p>
                    <span>{{ data.arrdate }}</span><br />
                    <span>{{ data.deptime }}</span>
                  </p>
                  <p>
                    <span>{{ data.depdate }}</span><br />
                    <span>{{ data.arrtime }}</span>
                  </p>
                </i>
                <i class="line"></i>
                <i>
                  <p>
                    <span>{{ data.depairpot }}</span><br />
                    <span>{{ data.depairpot_en }}</span>
                  </p>
                  <p>
                    <span>{{ data.arrairpot }}</span><br />
                    <span>{{ data.arrairpot_en }}</span>
                  </p>
                </i>
              </div>
              <div class="flight_detils">
                <p class="flight_d_span">
                  <span>成本中心/Cost Center</span>
                  <span>{{ data.cccode }}/{{ data.cccode2 }}</span>
                </p>
                <p class="flight_d_span">
                  <span>出差目的/Travel Purpose</span>
                  <span>{{ data.travel_purpose }}/{{
                      getTravel_purpose_en(data.travel_purpose)
                    }}</span>
                </p>
                <p class="flight_d_span flight_d_s_3">
                  <span>票号/Ticket No:</span>
                  <span v-if="data.ticketno">{{ data.ticketno }}</span>
                  <span v-else class="last"><b>-</b></span>
                </p>
                <p style="padding-bottom: 8px" class="flight_d_span">
                  <span>{{ data.cabin }}/{{ data.cabindetail }}</span>
                  <span class="price">￥{{ toMoney(data.price * 1 + data.tax * 1) }}</span>
                </p>
              </div>
            </div>
          </article>

          <div class="changing" v-if="trainData.length != 0">
            <span class="chang" v-if="trip_res.list[0].status == '已出票'">退票/Refund</span>

            <span class="chang" v-if="
                $route.query.auth == 0 &&
                  status == 3 &&
                  internationalData.length == 0 &&
                  (trip_res.list[0].status == '已出票' ||
                    trip_res.list[0].status == '被改签')
              " @click="ticketChange(trip_res)">改期/Reschedule</span>
          </div>
          <div class="sheet_frame" v-else>
            <span class="not_data">No data to display</span>
          </div>
        </div>
        <!-- 火车交通 Over -->

        <div class="sheet_frame" v-else>
          <span class="not_data">No data to display</span>
        </div>

        <!-- 交通费用总计 -->
        <div style="padding-top: 8px" class="flight_d_span_price">
          <p class="flight_d_span">
            <span>交通费用/Ttransportation Expense</span>
            <span class="price">￥{{ toMoney(all_price[0] + all_price[1] + all_price[2]) }}</span>
          </p>
          <p class="flight_d_span">
            <span>服务费/Service Fee</span>
            <span class="price">￥{{ toMoney(servicefee) }}</span>
          </p>
        </div>
        <!-- 交通费用总计 Over -->
      </div>
    </div>
    <!-- 交通box闭合 -->

    <!-- 住宿box -->
    <div class="sheet-box hotel_box">
      <div class="sheet-bor hotel_bor">
        <h5>
          酒店/Hotel

          <span class="add hotel_add" v-if="$route.query.auth == 0 && status == 1" @click="goPath('/hotel')"><van-icon name="plus" size="15" />新增/Add</span>
        </h5>
        <!-- 酒店 -->
        <div class="sheet_frame" v-if="hotelData.length != 0">
          <article v-for="(i, index) in hotelData" :key="index">
            <div>
              <span class="name_title"><van-icon size="19" :name="require('../../assets/image/name-icon.png')" />{{ i.name }}</span>
              <div class="outer_shell" v-for="(data, index) in i.infoData" :key="index">
                <div class="hotel_day flight_info">
                  <i style="width: 24%"></i>
                  <i style="width: 23%"></i>
                  <i><span>{{ data.night_qty }} Day {{ data.roomdescr }}</span>
                  </i>
                </div>
                <div class="flight_info">
                  <i>
                    <p>
                      <span>{{ data.checkindate }}</span><br />
                      <span>14:00</span>
                    </p>
                    <p>
                      <span>{{ data.checkoutdate }}</span><br />
                      <span></span>
                    </p>
                  </i>
                  <i class="line"></i>
                  <i>
                    <p>
                      <span>{{ data.hotelname }}</span><br />
                      <span>{{ data.hotelname_en }}</span>
                    </p>
                    <p>
                      <span>{{ data.arrairpot }}</span><br />
                      <span>{{ data.arrairpot_en }}</span>
                    </p>
                  </i>
                </div>
                <div class="flight_detils">
                  <p class="flight_d_span flight_d_s_1">
                    <span>成本中心/Cost Center</span>
                    <span>{{ data.cccode }}/{{ data.cccode2 }}</span>
                  </p>
                  <p class="flight_d_span flight_d_s_2">
                    <span>出差目的/Travel Purpose</span>
                    <span>{{ data.travel_purpose }}/{{
                        getTravel_purpose_en(data.travel_purpose)
                      }}</span>
                  </p>
                  <p class="flight_d_span flight_d_s_3">
                    <span>酒店费用</span>
                    <span class="price">￥{{ toMoney(data.price * 1) }}</span>
                  </p>
                  <p class="flight_d_span flight_d_s_4">
                    <span>支付方式/Payment</span>
                    <span v-if="data.payment_type == 'prepay'">预付/prepay</span>
                    <span v-if="data.payment_type != 'prepay'">现付/Postpay</span>
                  </p>

                  <div class="delete-box" v-if="
                      data.status != '已取消' &&
                        data.status != '已出票' &&
                        data.status != '已审批通过' &&
                        data.status != '已提交审批' &&
                        data.payment_remark != '不可取消' &&
                        ((data.confirmno &&
                          data.confirmno.indexOf('-') == -1) ||
                          data.source)
                    ">
                    <span class="delete" @click="delete_hotel(data)">删除/Delete</span>
                  </div>
                </div>
              </div>
            </div>
          </article>
        </div>
        <!-- 酒店 Over -->

        <div class="sheet_frame" v-else>
          <span class="not_data">No data to display</span>
        </div>

        <!-- 酒店费用总计 -->
        <div style="padding-top: 8px" class="flight_d_span_price">
          <p class="flight_d_span">
            <span>酒店费用/Hotel Expense</span>
            <span class="price">￥{{ toMoney(all_price[3]) }}</span>
          </p>
          <p class="flight_d_span">
            <span>服务费/Service Fee</span>
            <span class="price">￥0.00</span>
          </p>
        </div>
        <!-- 酒店费用总计 Over -->
      </div>
    </div>
    <!-- 住宿bo闭合 -->

    <!-- 订单总金额 -->
    <div class="sheet-box sum-box">
      <div class="sheet-bor sun-deilst-box">
        <i>
          <span class="sun_up1">订单总金额/Total</span>
          <span class="sun_up2">￥{{
              toMoney(
                parseFloat(servicefee) +
                  parseFloat(all_price[0]) +
                  parseFloat(all_price[1]) +
                  parseFloat(all_price[2]) +
                  parseFloat(all_price[3])
              )
            }}</span>
        </i>
        <i>
          <span>现付/Postpay</span>
          <span>￥0.00</span>
        </i>
        <i>
          <span>对公结算/Corporate Settlement</span>
          <span>0,0000.00</span>
        </i>
        <i>
          <span>审批时限/Approve Before</span>
          <!-- <span>{{ submt_info.tktdeadline }}</span> -->
          <span>{{ sussTime }}</span>
        </i>
      </div>
    </div>
    <!-- 订单总金额 Over -->

    <!-- 按钮 -->
    <div class="btn" v-if="this.$route.query.auth == 0 && status == 1">
      <div @click="submit(0)">
        <span>提交审批单</span>
        <span>Submit for Approval</span>
      </div>
      <div @click="submit(1)">
        <span>取消申请单</span>
        <span>Cancel Application</span>
      </div>
      <div @click="toPay" style="display: none;">
        <span>立即支付</span>
        <span>Pay Now</span>
      </div>
    </div>
    <div class="btn" v-if="this.$route.query.auth == 1 && status == 2">
      <div @click="submit(2)">
        <span>审批通过</span>
        <span>Approved</span>
      </div>
      <div @click="submit(3)">
        <span>拒绝审批</span>
        <span>Refuse approval</span>
      </div>
    </div>
    <!--  -->

    <!-- 提交遮罩层 -->
    <van-overlay :show="successBookShow" @click="successBookShow = false">
      <div class="successBook-box" @click.stop>
        <div class="book-deli">
          <div class="book-img">
            <img src="../../assets/image/booking_success.png" />
          </div>
          <div class="book-text">
            <i v-if="submt_pd == true">
              提交成功，最晚审批时限为<span>{{ sussTime }}</span>
              请提醒您的主管及快审批/Submit success!The deadline is<span>
                {{ sussTime }}</span>.Please contact your line manager to approve.
            </i>
            <i v-else>{{ submt_xx }}</i>
            <!-- <i
              >预订成功,最晚出票时限<span>{{ sussTime }}</span>
              请返回出差申请表中提交审批/The booking is successful.The latest
              time limit is<span> {{ sussTime }}</span
              >.Please return to the travel application form for approva</i
            > -->
          </div>

          <div class="next" @click="$router.push({ path: '/' })">
            <span>确认<br />Confirm</span>
          </div>
          <!-- <div class="book-close" @click="successBookShow = false">
            <van-icon name="close" size="32" color="white" />
          </div> -->
        </div>
      </div>
    </van-overlay>
    <!-- 遮罩层 Over -->
    <!-- 取消遮罩层 -->
    <van-overlay :show="clearBookShow" @click="clearBookShow = false">
      <div class="successBook-box" @click.stop>
        <div class="book-deli" style="height: 40%">
          <div class="book-img">
            <img src="../../assets/image/booking_clear.png" />
          </div>
          <div class="book-text">
            <i> {{ BookShowInfo }} <br />{{ BookShowInfo_en }} </i>
          </div>

          <div class="next" @click="submit_confirm(BookShowX)">
            <span>确认<br />Confirm</span>
          </div>
        </div>
        <div style="position: absolute; bottom: 25%">
          <van-icon name="close" size="30" color="#FFFFFF" @click="clearBookShow = false" />
        </div>
      </div>
    </van-overlay>
    <!-- 遮罩层 Over -->
  </div>
</template>
<script>
import { showDialog } from 'vant';

import axios from '../../utils/axiosLogin.js'
export default {
  data () {
    return {
      stepList: [
        {
          id: "1",
          show: true,
          show_line: true,
          stepIndex: 1,
          active: "Y",
          finish: true,
          title: "未提交审批",
          titleEn: "Not Submitted",
          icon: require("../../assets/image/notSub.png"),
        },
        {
          id: "2",
          show: false,
          show_line: true,
          stepIndex: 1,
          active: "Y",
          finish: false,
          title: "已提交审批",
          titleEn: "Pending Approval",
          icon: require("../../assets/image/wc.png"),
        },
        {
          id: "3",
          show: false,
          show_line: false,
          stepIndex: 4,
          active: "N",
          finish: false,
          title: "已出票",
          titleEn: "Issued",
          icon: require("../../assets/image/wc.png"),
        },
        {
          id: "4",
          show: true,
          show_line: false,
          stepIndex: 5,
          active: "N",
          finish: true,
          title: "已取消",
          titleEn: "Canceled",
          icon: require("../../assets/image/wc.png"),
        },
        {
          id: "5",
          show: false,
          show_line: false,
          stepIndex: 6,
          active: "N",
          finish: false,
          title: "被改签",
          titleEn: "Be Rascheduled",
          icon: require("../../assets/image/wc.png"),
        },
      ],
      //国内航班
      domesticData: [],
      //国际航班
      internationalData: [],
      //火车
      trainData: [],
