    getQueryString (name) {
      let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      let urlse = decodeURI(location.search);
      let r = urlse.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      return null;
    },
    //判断URL协议方案是https还是http
    getBaseUrl () {
      var ishttps = "https:" == location.protocol ? true : false;
      return ishttps;
