        "Business travel",
        "Company welfare leave",
        "Visiting customers",
        "Attending meetings",
      ]; //商务差旅//公司福利休假//拜访客户
      if (c == "出差旅费" || c == "商务差旅") {
        return e[0];
      } else if (c == "公司福利休假") {
        return e[1];
      } else if (c == "拜访客户") {
        return e[2];
      } else if (c == "参加会议") {
        return e[3];
      } else {
        return "";
      }
    },

