    setgroup (arr, jsn, ind) {
      let p = [];
      arr.map((v) => {
        if (jsn) {
          jsn[ind] += v.price * 1;
        }
        let hasn = false;
        p.map((v2) => {
          if (v2.name == v.paxname) {
            hasn = true;
            v2.infoData.push(v);
          }
        });
        if (!hasn) {
          p.push({
            name: v.paxname,
            isOpen: false,
            infoData: [v],
          });
        }
      });
      return p;
    },
    calc_time2 (t1, t2) {
      //2018/12/01 10:10;
      //console.log(t1,t2);
      var t1 = new Date(t1);
      var t2 = new Date(t2);
      //console.log(t1,t2);
      var t1t = t1.getTime();
      var t2t = t2.getTime();
      if (t1t > t2t) {
        var l = t1t - t2t;
      } else {
        var l = t2t - t1t;
      }
      //console.log(t1t,t2t);
      //console.log(l);
      return this.calc_time(l / 60000);
    },
    calc_time (time) {
      var hour = parseInt(time / 60);
      var min = time % 60;
      // console.log(hour,min)
      return hour + "H" + min + "M";
    },
    toMoney (num) {
      num = parseFloat(num) + "";
      var x = num.split(".");
      var y = x[0];
      var z = x[1];
      var y = (y || 0).toString(),
        result = "";
      while (y.length > 3) {
        result = "," + y.slice(-3) + result;
        y = y.slice(0, y.length - 3);
      }
      if (y) {
        result = y + result;
      }
      y = result;
      if (z) {
        if (z.length >= 2) {
          var f = z.substr(0, 2);
          var f0 = z.substr(2);
          if (f0 > 0) {
            f = f * 1 + 1;
            if (f == 100) {
              y = y * 1 + 1;
              f = "00";
            }
          }
          return y + "." + f;
        } else if (z.length == 1) {
          return y + "." + z + "0";
        } else if (z.length == 0) {
          return y + "." + "00";
        }
      } else {
        return y + ".00";
      }
    },
