    delete_hotel (obj) {
      var that = this;

      showDialog({
        title: "提示/Notice",
        message: "您确定取消房间吗？/Confirm to cancel the booking?",
        theme: "round-button",
        confirmButtonText: "确定/Confirm",
        confirmButtonColor: "linear-gradient(160deg, #0274c9 0%, #044db5 100%)", //确定按钮文字
        showCancelButton: true,
        cancelButtonText: "取消/Cancel",
        cancelButtonColor:
          "linear-gradient(160deg, rgb(199 199 199) 0%, rgb(173 173 173) 100%)",
      }).then(() => {
        // that.upload();
        that
          .$axios({
            method: "POST",
            url:
              "https://ezway.efasco.com/HotelService.asmx/getELONGHotelCancel",
            data: JSON.stringify({
              OrderId: obj.confirmno,
              CancelCode: "其他",
              Reason: "cancel",
              PenaltyAmount: "",
            }),
          })
          .then(
            (res) => {
              // that.$toast.clear();
              var json = res.data;
              console.log("取消1", json);
              if (
                json.Code == "0" &&
                json.Result.Successs &&
                json.Result.Successs == "true"
              ) {
                that
                  .$axios({
                    type: "GET",
                    url:
                      "https://ezway.efasco.com/HotelService.asmx/CancelExorder?ex_no=" +
                      obj.ex_no,
                    dataType: "text",
                  })
                  .then(
                    () => {
                      var json1 = res1.data;
                      console.log(json1);
                      if (json1 == "success") {
                        that
                          .$dialog({
                            title: "提示/Notice",
                            message: "取消成功/Cancel successfully!",
                            theme: "round-button",
                            confirmButtonText: "确定/Confirm", //改变确认按钮上显示的文字
                            confirmButtonColor:
                              "linear-gradient(160deg, #0274c9 0%, #044db5 100%)", //确定按钮文字
                          })
                          .then(() => {
                            this.$router.push({
                              path: "/home",
                            });
                          });
                      } else {
                        that.getDialog("取消失败/Cancel failed");
                      }
                    },
                    (e) => {
                      console.log(e);
                      that.getDialog("取消失败/Cancel failed");
                    }
                  );
              } else {
                if (json.Code.indexOf("|") != -1) {
                  if (json.Code.indexOf("订单已经处于取消状态") != -1) {
                    that.getDialog(
                      "取消失败，订单已经处于取消状态/Cancel failed. The booking was already cancelled."
                    );
                    return;
                  }
                  that.getDialog(
                    "取消失败/Cancel failed," + json.Code.split("|")[1]
                  );
                } else {
                  that.getDialog("取消失败/Cancel failed");
                }
              }
            },
            (error) => {
              // that.$toast.clear();
              that.getDialog("取消失败/Cancel failed");
            }
          );
      });
    },
