    //提交确认
    async submit_confirm (x) {
      let a = this.$store.state.CltId_set;
      console.log(a);
      console.log(this.$route);
      let url = "";
      // this.upload();
      let allc = [];
      var that = this;

      //提交申请
      if (x === 0) {
        //航班判断
        this.clearBookShow = false;
        if (this.trip_res.list[0].trips.length > 0) {
          let t_t = this.trip_res.list[0].trips;

          let busy_time = [];
          for (let iit1 = t_t.length - 1; iit1 >= 0; iit1--) {
            let t = t_t[iit1];
            let got = t.depdate.replace(/-/g, "/") + " " + t.deptime;
            let arrt = t.arrdate.replace(/-/g, "/") + " " + t.arrtime;
            for (let iy = busy_time.length - 1; iy >= 0; iy--) {
              let ixy = busy_time[iy].split("-");
              let p1 = ixy[0];
              let got1 = new Date(ixy[1]).getTime() * 1;
              let arr1 = new Date(ixy[2]).getTime() * 1;
              let got2 = new Date(got).getTime() * 1;
              let arr2 = new Date(arrt).getTime() * 1;
              if (
                t.paxname == p1 &&
                ((got1 <= got2 && arr1 >= got2) ||
                  (got2 <= got1 && arr2 >= got1))
              ) {
                this.dialogMseeage(
                  "您的行程时间有冲突，请再确认。Your trip arrangement is unreasonable, please confirm again."
                );
                // this.$toast.clear();
                return;
              }
            }
            busy_time.push(t.paxname + "-" + got + "-" + arrt);
            // console.log(busy_time);
          }
          for (let i = 0; i < t_t.length; i++) {
            this.clearBookShow = false;
            if (!t_t[i].freecanceldeadline) {
              // this.upload();
              this.dialogMseeage(
                "您的订单已生成，当前订单中有行程未确认最晚出票时限，无法提交审批！请在收到提醒短信后提交审批。Your order has been generated，approval cannot be submitted due to deadline of the itinerary not confirmed. Please submit after receiving the reminder message.！"
              );

              return;
            } else {
              /*  */
              this.clearBookShow = false;
              let this_time = new Date(
                t_t[i].freecanceldeadline.replace(/-/g, "/")
              ).getTime();
              console.log(this_time);
              let timeoffset = new Date().getTimezoneOffset() + 480;
              this_time = this_time - timeoffset * 60000;
              let now_time = new Date().getTime();
              if (now_time > this_time && t_t[i].routetype != "D") {
                this.dialogMseeage(
                  "当前订单中行程的最晚出票时限已失效，无法提交审批，请及时联系旅行顾问。Approval cannot be submitted due to the approve expire date expired, please contact the travel consultant."
                );
                return;
              }
            }
          }
        }
        //酒店判断
        if (this.trip_res.list[0].hotels.length > 0) {
          this.clearBookShow = false;
          let t_h = this.trip_res.list[0].hotels;
          for (let ii = 0; ii < t_h.length; ii++) {
            if (!t_h[ii].freecanceldeadline) {
              if (t_h[ii].source) {
                this.dialogMseeage(
                  "您的订单已生成，当前订单中有酒店未确认最晚取消时间，无法提交审批！请在收到提醒短信后提交审批。Your order has been generated，approval cannot be submitted due to deadline of the hotel booking not confirmed. Please submit after receiving the reminder message."
                );

                return;
              } else {
                this.dialogMseeage(
                  "您预订的协议酒店虚拟订单已生成，FASCO后台半小时内完成预订，完成后将发送确认信息并代为提交审批。"
                );

                return;
              }
            } else {
              this.clearBookShow = false;
              let this_time = new Date(
                t_h[ii].freecanceldeadline.replace(/-/g, "/")
              ).getTime();
              let timeoffset = new Date().getTimezoneOffset() + 480;
              this_time = this_time - timeoffset * 60000;
              let now_time = new Date().getTime();
              if (
                now_time > this_time &&
                t_h[ii].payment_remark != "不可取消"
              ) {
                this.dialogMseeage(
                  "当前订单中行程的最晚出票时限已失效，无法提交审批，请及时联系旅行顾问。Approval cannot be submitted due to the approve expire date expired, please contact the travel consultant."
                );

                return;
              }
            }
          }
        }

        let naddx = "&tr_no=" + this.trip_res.eorder_no;

        console.log(this.trip_res);
        // if (a.notneed_tr_no) naddx = "&tr_no=";

        // this.$toast.clear();

        this.submit_get_suss(
          "https://ezway.efasco.com/ErpService.asmx/SaveUniqTR?staffno=" +
          this.booker.StaffNo +
          "&guid=" +
          this.$route.query.orderid +
          naddx
        );
      } else if (x === 1) {
        //取消申请
        this.clearBookShow = false;
        let rs_hotels = this.trip_res.list[0].hotels;
        // console.log(this.trip_res)

        if (rs_hotels.length) {
          console.log("酒店");
          allc.length = rs_hotels.length; //酒店数组长度
          var has_c_e = false;
          // this.upload();
          for (let ih = rs_hotels.length - 1; ih >= 0; ih--) {
            allc[ih] = false;
            // console.log(rs_hotels[ih].status,rs_hotels[ih].payment_remark,rs_hotels[ih].confirmno,rs_hotels[ih].source)
            if (
              rs_hotels[ih].status != "已取消" &&
              rs_hotels[ih].payment_remark != "不可取消" &&
              ((rs_hotels[ih].confirmno &&
                rs_hotels[ih].confirmno.indexOf("-") == -1) ||
                rs_hotels[ih].source)
            ) {
              has_c_e = true;
              console.log("取消1");
              this.cancel_hotel_f(rs_hotels[ih], ih, function (bool, ind) {
                allc[ind] = bool;
                var allo = true;

                for (var i = allc.length - 1; i >= 0; i--) {
                  console.log(1);
                  if (!allc[i]) allo = false;
                }
                if (allo) {
                  console.log(2);
                  // cancel_tr(result);
                  that.submit_get(
                    "https://ezway.efasco.com/ErpService.asmx/CancelEorder" +
                    "?ex_no=" +
                    that.trip_res.eorder_no
                  );
                }

                // console.log(allc);
              });
            }
            if (has_c_e) {
              return;
            }

            // if (rs_hotels[i].status == "已确认") {
            //   all_c[ih] = false;

            //   console.log(rs_hotels[i]);
            // }
            // this.$toast.clear();
          }

          // if (has_c_e) {
          //   return;
          // }
          console.log("直接取消");
          console.log(this.trip_res.eorder_no);
          this.submit_get(
            "https://ezway.efasco.com/ErpService.asmx/CancelEorder" +
            "?ex_no=" +
            this.trip_res.eorder_no
          );
        } else {
          console.log("行程");
          this.submit_get(
            "https://ezway.efasco.com/ErpService.asmx/CancelEorder" +
            "?ex_no=" +
            this.trip_res.eorder_no
          );
        }
      } else if (x === 2) {
        //通过审批
        this.clearBookShow = false;
        this.submit_get_Approval(
          `https://ezway.efasco.com/ErpService.asmx/ApproveExorder?exno=${this.trip_res.eorder_no}&approverid=${this.booker.Identification}`
        );
      } else if (x === 3) {
        //拒绝审批
        this.clearBookShow = false;

        this.submit_get_Eorde(
          `https://ezway.efasco.com/ErpService.asmx/UnApproveEorder?eno=${this.trip_res.eorder_no}&approverid=${this.booker.Identification}`
        );

        // this.submit_get(
        //   "https://ezway.efasco.com/ErpService.asmx/CancelEorder?ex_no=" +
        //     this.trip_res.eorder_no
        // );
      }
    },

    async submit_get (url, message) {
      if (sessionStorage.getItem("nosubmit")) {
        this.getDialog("操作成功/Operate successfully!");
        return;
      }

      // this.upload();
      const g = await axios.get(url).catch((e) => {
        this.getDialog("提交失败/Submit fail!");
      });
      // this.$toast.clear();
      // console.log(g);
      let res = g.data;
      if (
        res == "success" ||
        res == '"success"' ||
        res.indexOf("success") != -1
      ) {
        showDialog({
          title: "提示/Notice",
          message: "操作成功/Operate successfully!",
          theme: "round-button",
          confirmButtonText: "确定/Confirm",
          confirmButtonColor:
            "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
        }).then(() => {
          this.getTrip();
          // if (this.$route.path == "/applicationdetail") {
          //   this.$router.push({
          //     path: "/approveform",
          //   });
          // }
          this.$router.go(-1);
        });
      } else {
        this.getDialog("提交失败/Submit fail!");
      }
    },

    async submit_get_suss (url) {
      if (sessionStorage.getItem("nosubmit")) {
        this.getDialog("操作成功/Operate successfully!");
        return;
      }

      // this.upload();
      const g = await axios.get(url).catch((e) => {
        this.getDialog("提交失败/Submit fail!");
      });
      // this.$toast.clear();
      // console.log(g);
      let res = g.data;
      if (
        res == "success" ||
        res == '"success"' ||
        res.indexOf("success") != -1
      ) {
        this.successBookShow = true; //信息框弹出
        this.sussTime = this.trip_res.tktdeadline; //时间信息
        this.submt_pd = true; //显示成功信息

        this.getTrip();
      } else {
        this.getDialog("提交失败/Submit fail!");
      }
    },
    async submit_get_Approval (url) {
      //提交审批
      // this.upload();
      const g = await axios.get(url).catch((e) => {
        this.getDialog("通过审批失败/Failed to approve!");
      });

      // this.$toast.clear();
      let res = g.data;
      if (
        res == "success" ||
        res == '"success"' ||
        res.indexOf("success") != -1
      ) {
        showDialog({
          title: "提示/Notice",
          message: "通过审批成功/Approved succesfully!",
          theme: "round-button",
          confirmButtonText: "确定/Confirm",
          confirmButtonColor:
            "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
        }).then(() => {
          this.getTrip();
          this.$router.go(-1);
        });
      } else {
        this.getDialog("通过审批失败/Failed to approve!");
      }
    },

    async submit_get_Eorde (url) {
      //拒绝审批
      // this.upload();
      const g = await axios.get(url).catch((e) => {
        this.getDialog("拒绝审批失败/Refuse approval fail!");
      });

      // this.$toast.clear();
      let res = g.data;
      if (
        res == "success" ||
        res == '"success"' ||
        res.indexOf("success") != -1
      ) {
        showDialog({
          title: "提示/Notice",
          message: "拒绝审批成功/Refuse approval succesfully!",
          theme: "round-button",
          confirmButtonText: "确定/Confirm",
          confirmButtonColor:
            "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
        }).then(() => {
          this.getTrip();
          // this.$router.push({
          //   path: "/",
          // });
          this.$router.go(-1);
        });
      } else {
        this.getDialog("拒绝审批失败/Refuse approval fail!");
      }
    },

    getDialog (message) {
      showDialog({
        title: "提示/Notice",
        message: message,
        theme: "round-button",
        confirmButtonText: "确定/Confirm",
        confirmButtonColor: "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
      });
    },
    dialogMseeage (message) {
      setTimeout(() => {
        showDialog({
          title: "提示/Notice",
          message: message,
          theme: "round-button",
          confirmButtonText: "确定/Confirm",
          confirmButtonColor:
            "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
        });
        // this.$toast.clear();
      }, 1500);
    },
  },
};
