<template>
  <div class="overall">
    <van-sticky>
      <header class="head">
        <div class="head-title">
          <van-icon name="arrow-left" size="23" @click="$router.go(-1)" />
          <div>
            <i>
              <!-- <span>{{
                    search.cities[now_trip == "0" ? 0 : 1].CityNameChinese +
                    "-" +
                    $route.query.flight_doc_or_int ==
                  1
                ? search.cities[now_trip == "0" ? 1 : 0].CityNameChinese
                : search.cities[now_trip == "0" ? 2 : 0].CityNameChinese
            }}</span> -->
              <span v-if="$route.query.flight_doc_or_int == 1">{{
                search.cities[now_trip == "0" ? 0 : 1].CityNameChinese +
                  "-" +
                  search.cities[now_trip == "0" ? 1 : 0].CityNameChinese
              }}</span>
              <span v-else>
                <!-- {{
                  search.cities[now_trip == "0" ? 0 : 2].CityNameChinese +
                  "-" +
                  search.cities[now_trip == "0" ? 2 : 0].CityNameChinese
                }} -->
                {{
                  search.cities[now_trip == "0" ? 0 : 1].CityNameChinese +
                    "-" +
                    search.cities[now_trip == "0" ? 1 : 0].CityNameChinese
                }}
              </span>
              <span>&nbsp;{{
                  getDay(search.date[now_trip == "0" ? 0 : 1]) +
                    " " +
                    getWeek(search.date[now_trip == "0" ? 0 : 1])
                }}</span>
            </i>
            <i>
              <span v-if="$route.query.flight_doc_or_int == 1">
                {{
                  search.cities[now_trip == "0" ? 0 : 1].CityNameEnglish +
                    "-" +
                    search.cities[now_trip == "0" ? 1 : 0].CityNameEnglish
                }},
              </span>
              <span v-else>
                <!-- {{
                  search.cities[now_trip == "0" ? 0 : 2].CityNameEnglish +
                  "-" +
                  search.cities[now_trip == "0" ? 2 : 0].CityNameEnglish
                }}, -->
                {{
                  search.cities[now_trip == "0" ? 0 : 1].CityNameEnglish +
                    "-" +
                    search.cities[now_trip == "0" ? 1 : 0].CityNameEnglish
                }},
              </span>
              <span> {{ enDay(search.date[now_trip == "0" ? 0 : 1]) }}, </span>
              <span>
                {{ enMonth(search.date[now_trip == "0" ? 0 : 1]) }}
              </span>
            </i>
          </div>

          <!-- <i class="title_small" v-if="now_trip!=0">
          <span @click="now_trip=0">去程</span>
        </i> -->
        </div>
      </header>
    </van-sticky>

    <article>
      <!-- 国内去程 -->
      <section>
        <div class="flight_list_content flight-line_2" v-if="now_trip != 0 && $route.query.flight_doc_or_int == 1">
          <div>
            <div class="flight_conlist_d">
              <div class="fli_i">
                <div class="fli_item">
                  <div class="flight-line_3"><span>去程</span></div>
                  <div class="fli_item_1">
                    <img width="26" height="22" alt="" :src="
                        require('../../assets/image/fli/' +
                          choose_flight[0].seg.segments[0].aircompany +
                          '.png')
                      " />
                    <span>{{
                      choose_flight[0].seg.segments[0].companyname
                    }}</span>
                    <span>{{
                      choose_flight[0].seg.segments[0].companyname_en
                    }}</span>
                    <span style="font-size: 15px; color: #eb6301">{{
                      choose_flight[0].seg.segments[0].airline
                    }}</span>
                  </div>
                  <div class="fli_item_2"></div>
                  <div class="fli_item_3">
                    <i>
                      <span><span style="
                            color: #eb6301;
                            font-size: 16px;
                            font-weight: 600;
                          ">{{ choose_flight[0].seg.segments[0].deptime }}</span>
                        {{ choose_flight[0].seg.segments[0].orgpotname }}
                        &nbsp;{{ choose_flight[0].seg.segments[0].dstTerminal }}
                      </span>
                      <br />
                      <span>{{
                          choose_flight[0].seg.segments[0].orgpotnameen
                        }}&nbsp;{{
                          choose_flight[0].seg.segments[0].dstTerminal
                        }}</span>
                    </i>
                    <i class="when">{{
                      choose_flight[0].seg.segments[0].duration2
                    }}</i>
                    <i>
                      <span><span style="
                            color: #eb6301;
                            font-size: 16px;
                            font-weight: 600;
                          ">{{
                            choose_flight[0].seg.segments[0].arritime2
                          }}</span>
                        {{ choose_flight[0].seg.segments[0].dstpotname }}
                        &nbsp;{{
                          choose_flight[0].seg.segments[0].orgTerminal
                        }}</span>
                      <br />
                      <span>{{
                          choose_flight[0].seg.segments[0].dstpotnameen
                        }}&nbsp;{{
                          choose_flight[0].seg.segments[0].orgTerminal
                        }}</span>
                    </i>
                  </div>

                  <div class="fli_item_4">
                    <i class="price back_price">
                      <span @click="$router.go(-1)">重选去程</span>

                      <span>￥{{
                          toMoney(
                            parseFloat(choose_flight[0].cabin.singlePrice) +
                              parseFloat(choose_flight[0].cabin.airportTax)
                          )
                        }}</span>
                    </i>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <!-- 国际去程 -->
      <section>
        <div class="flight_list_content flight-line_2" v-if="now_trip != 0 && $route.query.flight_doc_or_int == 2">
          <div>
            <div class="flight_conlist_d">
              <div class="fli_i">
                <div class="fli_item">
                  <div class="flight-line_3"><span>去程</span></div>
                  <div class="fli_item_1">
                    <img width="26" height="22" alt="" :src="
                        require('../../assets/image/fli/' +
                          choose_flight[0].seg.AirlineCode +
                          '.png')
                      " />
                    <span>{{ choose_flight[0].seg.AirlineNameCN }}</span>
                    <span>{{ choose_flight[0].seg.AirlineNameEN }}</span>
                    <span style="font-size: 15px; color: #eb6301">{{
                      choose_flight[0].data.Sectors[0].FlightDescr
                    }}</span>
                  </div>
                  <div class="fli_item_2"></div>
                  <div class="fli_item_3">
                    <i>
                      <span><span style="
                            color: #eb6301;
                            font-size: 16px;
                            font-weight: 600;
                          ">{{ choose_flight[0].seg.OrigTime2 }}</span>
                        {{ choose_flight[0].seg.OrigAPNameCN }} &nbsp;{{
                          choose_flight[0].seg.OrigT
                        }}
                      </span>
                      <br />
                      <span>{{ choose_flight[0].seg.OrigAPNameEN }}&nbsp;{{
                          choose_flight[0].seg.OrigT
                        }}</span>
                    </i>
                    <i class="when">{{ choose_flight[0].seg.Duration2 }}</i>
                    <i>
                      <span><span style="
                            color: #eb6301;
                            font-size: 16px;
                            font-weight: 600;
                          ">{{ choose_flight[0].seg.DestTime2 }}</span>
                        {{ choose_flight[0].seg.DestAPNameCN }} &nbsp;{{
                          choose_flight[0].seg.DestT
                        }}</span>
                      <br />
                      <span>{{ choose_flight[0].seg.DestAPNameEN }}&nbsp;{{
                          choose_flight[0].seg.DestT
                        }}</span>
                    </i>
                  </div>

                  <div class="fli_item_4">
                    <i class="price back_price">
                      <span @click="$router.go(-1)">重选去程</span>
                      <!-- 暂时隐藏 -->
                      <span>￥{{
                          toMoney(
                            parseFloat(choose_flight[0].cabin.TaxAmt) +
                              parseFloat(choose_flight[0].cabin.TicketAmt)
                          )
                        }}</span>
                    </i>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <!--  -->
      <!-- 航班列表开始 -->
      <section v-for="(data, i) in get_filter_flight_list" :key="i">
        <!-- 国内航班 -->
        <div class="flight_list_content" v-if="
            $route.query.flight_doc_or_int == 1 && data.segments[0].cabins[0]
          ">
          <div v-for="(item, j) in data.segments" :key="j">
            <div class="flight_conlist_d">
              <div class="fli_i" @click="onOpen(i)">
                <div class="fli_item">
                  <div class="fli_item_1">
                    <img width="26" height="22" alt="" :src="
                        require('../../assets/image/fli/' +
                          item.aircompany +
                          '.png')
                      " />
                    <span>{{ item.companyname }}</span>
                    <span>{{ item.companyname_en }}</span>
                    <span style="font-size: 15px; color: #eb6301">{{
                      item.airline
                    }}</span>
                  </div>
                  <div class="fli_item_2"></div>
                  <div class="fli_item_3">
                    <i>
                      <span><span style="
                            color: #eb6301;
                            font-size: 16px;
                            font-weight: 600;
                          ">{{ item.deptime }}</span>
                        {{ item.orgpotname }} &nbsp;{{ item.dstTerminal }}
                      </span>
                      <br />
                      <span>{{ item.orgpotnameen }}&nbsp;{{
                          item.dstTerminal
                        }}</span>
                    </i>
                    <i class="when">{{ item.duration2 }}</i>
                    <i>
                      <span><span style="
                            color: #eb6301;
                            font-size: 16px;
                            font-weight: 600;
                          ">{{ item.arritime2 }}</span>
                        {{ item.dstpotname }} &nbsp;{{ item.orgTerminal }}</span>
                      <br />
                      <span>{{ item.dstpotnameen }}&nbsp;{{
                          item.orgTerminal
                        }}</span>
                    </i>
                    <i class="price" style="bottom:35%">
                      <span style="font-size:16px">￥{{
                          toMoney(
                            parseFloat(item.cabins[0].singlePrice) +
                              parseFloat(item.cabins[0].airportTax)
                          )
                        }}</span>
                    </i>
                  </div>
                </div>
              </div>
              <!-- 展开列表 -->
              <transition name="fade">
                <!-- <div class="particulars" v-if="i == isOpen"> 点击第一个li，第一个li展开，点击第二个li，第一个li闭合，第二个li展开 -->
                <div class="particulars" style="display: none" :ref="i">
                  <div class="par_item" v-for="(value, k) in item.cabins" :key="k">
                    <div>
                      <i>
                        <span style="color: #eb6301; top: 8px">￥{{
                            toMoney(
                              parseFloat(value.singlePrice) +
                                parseFloat(value.airportTax)
                            )
                          }}&nbsp; ({{ get_discountRate(value) }}%)</span>
                        <!-- <span @click="GetLimitInfo(data, value)"
                        >退改签规则<br />Policy</span
                      > -->
                        <span></span>
                        <!-- <span
                        >￥{{
                          toMoney(
                            parseFloat(value.SinglePrice) +
                              parseFloat(value.AirportTax)
                          )
                        }}<br />(86%)</span
                      >
                      <div class="book" @click="choose(data, value, k)">
                        预定/book
                      </div> -->
                        <span></span>
                        <div class="book" @click="choose(data, value, k, 1)">
                          预订/book
                        </div>
                        <span class="par_item_txt">
                          <i>{{ value.cabinname }}{{ value.cabinType }}</i>
                          <i style="white-space: nowrap">{{
                            getCabinTypeEn(value.cabinname)
                          }}</i>
                        </span>
                        <!-- $dialog({
                            title: '行李限额',
                            theme: 'round-button',
                            message: value.Baggage,
                          }) -->
                        <span @click="isLuggage(value.baggage)" class="par_item_txt"><i>行李限额</i><i>Baggage Allowances</i></span>

                        <span class="par_item_txt" @click="GetLimitInfo(data, value)"><i>退改签规则</i><i>Policy</i></span>
                      </i>
                    </div>
                  </div>
                </div>
              </transition>
              <!-- 展开列表结束 -->
            </div>
          </div>
        </div>
        <!-- 国际航班 -->
        <div class="flight_list_content Intflight_list_content" v-else>
          <template v-for="gk in data.Sectors">
            <div v-for="(item, j) in gk.Segments" :key="j">
              <div class="flight_conlist_d">
                <div class="fli_i" @click="onOpen(i)" ref="fli_i_">
                  <div class="fli_item">
                    <div class="fli_item_1">
                      <img width="26" height="22" alt="" :src="
                          require('../../assets/image/fli/' +
                            item.AirlineCode +
                            '.png')
                        " />
                      <span>{{ item.AirlineNameCN }}</span>
                      <span>{{ item.AirlineNameEN }}</span>
                      <span style="font-size: 15px; color: #eb6301">{{
                        gk.FlightDescr
                      }}</span>
                    </div>
                    <div class="fli_item_2"></div>
                    <div class="fli_item_3">
                      <i>
                        <span><span style="
                              color: #eb6301;
                              font-size: 16px;
                              font-weight: 600;
                            ">{{ item.OrigTime2 }}</span>
                          {{ item.OrigAPNameCN }} &nbsp;{{ item.OrigT }}
                        </span>
                        <br />
                        <span>{{ item.OrigAPNameEN }}&nbsp;{{ item.OrigT }}</span>
                      </i>
                      <i class="when">{{ item.Duration2 }}</i>
                      <i>
                        <span><span style="
                              color: #eb6301;
                              font-size: 16px;
                              font-weight: 600;
                            ">{{ item.DestTime2 }}</span>
                          {{ item.DestAPNameCN }} &nbsp;{{ item.DestT }}</span>
                        <br />
                        <span>{{ item.DestAPNameEN }}&nbsp;{{ item.DestT }}</span>
                      </i>
                      <i class="price" style="bottom:35%">
                        <span style="font-size:16px">￥{{
                            toMoney(
                              data.Fares[0].TicketAmt + data.Fares[0].TaxAmt
                            )
                          }}</span>
                      </i>
                    </div>
                  </div>
                </div>
                <!-- <div class="particulars" v-if="i == isOpen">
                  <div class="par_item" v-for="(value,k) in data.Fares" :key="k">
                    <div>
                      <i>
                        1
                      </i>
                    </div>
                  </div>
                </div> -->
                <!-- 展开列表 -->
                <transition name="fade">
                  <div class="particulars" style="display: none" :ref="i">
                    <div class="int_detail">
                      <div class="flight-int-box flight-int1">
                        <span>第一程</span>
                        <span>机型Type: &nbsp;{{
                            item.AircraftCode.split("|")[0]
                          }}
                          &nbsp;</span>
                        <span style="
                            margin-left: 5px;
                            font-size: 15px;
                            color: #eb6301;
                          ">{{ item.AirlineCodeReal
                          }}{{ item.FlightNoReal }}</span>
                      </div>
                      <div class="flight-int-box flight-int2">
                        <div>
                          <span>{{ item.OrigTime2 }}</span>
                          <span>{{ item.OrigAPNameCN }}&nbsp;{{ item.OrigT }}</span>
                        </div>
                        <div style="width: 55px; height: 33px">
                          <img src="../../assets/image/flight_to.png" alt="" />
                        </div>
                        <div>
                          <span>{{ item.DestTime2 }}</span>
                          <span>{{ item.DestAPNameCN }}&nbsp;{{ item.DestT }}</span>
                        </div>
                      </div>
                      <div class="flight-int-box flight-int3">
                        <div>
                          <span style="font-size: 10px">飞行时长/Duration:&nbsp;{{ item.Duration2 }}</span>
                        </div>
                      </div>
                    </div>
                    <div class="par_item" v-for="(value, k) in data.Fares" :key="k">
                      <div>
                        <div class="flight-int_item">
                          <div class="fli_item_sc fli_item_sc1">
                            <div style="">
                              <span class="fli_item_dx">第一程</span>
                            </div>
                            <div v-for="(cabin, j) in value.SegmentCabins" :key="j">
                              <span>{{ cabin.CabinName }} {{ cabin.CabinType }}
                                <br />{{
                                  getCabinTypeEn(cabin.CabinName)
                                }}</span>
                            </div>
                            <div v-for="(cabin, j) in value.SegmentCabins" :key="j + 1">
                              <span style="display: flex"><img src="../../assets/image/flight_xlx.svg" alt="" />
                                {{ cabin.BaggageValue }}</span>
                            </div>
                          </div>

                          <div class="fli_item_sc fli_item_sc2">
                            <div>
                              <span style="color: #eb6301; top: 8px">￥{{
                                  toMoney(
                                    parseFloat(value.TicketAmt) +
                                      parseFloat(value.TaxAmt)
                                  )
                                }}</span>
                            </div>
                            <div v-for="(cabin, j) in value.SegmentCabins" :key="j + 2" class="book" @click="choose(item, value, j, 2, data)">
                              预订/book
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </transition>
                <!-- 展开列表结束 -->
              </div>
            </div>
          </template>
        </div>
      </section>
      <!-- 航班列表结束 -->
    </article>
    <!-- 底部工具 -->
    <transition name="bottool">
      <div class="bottom-toolbars" v-if="bomToolFlag">
        <van-grid :border="false" :icon-size="22">
          <div class="btn-too-box">
            <van-grid-item @click="sort_by(0)" icon="after-sale" :text="sort_text(0)" />
            <span>Price</span>
          </div>

          <div class="btn-too-box">
            <van-grid-item @click="sort_by(1)" icon="underway-o" :text="sort_text(1)" />
            <span>Time</span>
          </div>
          <div class="btn-too-box">
            <van-grid-item @click="sort_by(2)" icon="notes-o" :text="sort_text(2)" />
            <span>depart</span>
          </div>

          <div class="btn-too-box">
            <van-grid-item icon="filter-o" text="筛选" @click="onFiltrate" />
            <p>Filter</p>
          </div>
        </van-grid>
      </div>
    </transition>

    <!-- 底部工具结束 -->
    <!-- 筛选弹出层 -->
    <van-action-sheet v-model:show="filtrateShow" close-on-click-action close-icon="close" title="筛选/Filter">
      <hr class="delay-line" />
      <div class="filtrate">
        <!-- <van-cell-group>
          <van-cell title="起飞时间/Depart">
            <template #label>
              <van-dropdown-menu>
                <van-dropdown-item v-model="value1" :options="option1" />
              </van-dropdown-menu>
            </template>
          </van-cell>
        </van-cell-group> -->
        <van-tree-select :items="filtrates" v-model:main-active-index="active">
          <template #content>
            <!--  v-if="active === index" -->
            <div style="text-align: left" v-for="(value, key, index) in filter_json" :key="index">
              <div v-if="active === index">
                <div class="filtrate-content">
                  <van-checkbox-group v-model="filter_json_select[key]" ref="checkboxGroup" :max="key == '起飞时间' ? '1' : '5'">
                    <!-- v-if="key!='起飞时间'" -->

                    <span class="unlimited" :class="[
                        filter_json_ctrl[key] ? 'onUnlimited' : 'noUnlimited',
                      ]" @click="handleUnlimited_group(key, index)">
                      <span>不限</span>
                      <span>/unlimited</span>
                    </span>
                    <van-cell-group>
                      <van-cell v-for="(item, k2, index2) in value" clickable :key="index2" :icon="item.icon" :title="item.t" size="2rem" @click="toggle(index2, key)">
                        <template>
                          <span>{{ item.te }}</span>
                        </template>
                        <template #right-icon>
                          <van-checkbox :name="item.t" ref="checkboxes">
                            <template #icon="props">
                              <img class="img-icon" :src="props.checked ? activeIcon : inactiveIcon" />
                            </template>
                          </van-checkbox>
                        </template>
                      </van-cell>
                    </van-cell-group>
                  </van-checkbox-group>
                </div>
              </div>
            </div>
          </template>
        </van-tree-select>
        <van-button class="confirm" type="info" @click="confirm_filter">确定<br />Confirm</van-button>
      </div>
    </van-action-sheet>
    <!-- 筛选弹出层over -->
    <!-- 经济舱高价位弹出层 -->
    <div class="lfp2_box">
      <!-- v-if="
          $store.state.CltId_set.f_thisc_lowc_2 ||
          $store.state.CltId_set.need_lowest_c
        " -->
      <van-popup class="select_popup" v-model:show="lowest_ctr" round position="bottom">
        <div class="lfp2_title">
          根据贵司差旅政策，推荐您预订最低价航班
          <br />
          Optimal recommended fare available.
          <span style="position: relative; top: -10px; left: 12px" @click="lowest_ctr = false"><van-icon name="close" size="23" color="#8B8B8B" /></span>
        </div>

        <!-- <div class="lfp2_cont" v-for="(data,i) in lowest_c" :key="i"> -->
        <div v-for="(data, i) in lowest_c" :key="i" class="lfp2_cont" :class="{ lfp_con_act: lowest_active == i }" @click="lowest_active = i">
          <div class="lfp2_cont_title">
            <!-- <img :src="require('../../assets/image/fli/'+data.Aircompany+'.png')" alt=""/> -->
            <img :src="
                require('../../assets/image/fli/' + data.aircompany + '.png')
              " alt="" />
            <span>{{ data.aircompanyName }}航空 {{ data.airline }}
              {{ data.cabins[0].cabinname }}{{ data.cabins[0].cabinType }}
              {{ data.planestyle }} ({{
                (data.cabins[0].discountRate / 10).toFixed(1)
              }}折)</span>
          </div>

          <div class="lfp2_cont_item">
            <div class="lfp2_item_date">
              <div>{{ data.deptime }}</div>
              <div>{{ data.arritime2 }}</div>
            </div>
            <div class="lfp2_item_line"></div>
            <div class="lfp2_item_deli">
              <i>
                <span>{{ data.orgpotname }}</span><br />
                <span class="airport_en">{{ data.orgpotnameen }}</span>
              </i>
              <p>{{ data.duration2 }}</p>
              <i>
                <span>{{ data.dstpotname }}</span><br />
                <span class="airport_en">{{ data.dstpotnameen }}</span>
              </i>
            </div>
            <div class="lfp2_item_price">
              ￥{{
                toMoney(
                  parseFloat(data.cabins[0].singlePrice) +
                    parseFloat(data.cabins[0].airportTax)
                )
              }}
            </div>
          </div>
        </div>
        <div class="lfp2_btn">
          <div @click="book_low(true)" class="lfp2_btn_y">
            <span>预订最低价<br />Yes,Please</span>
          </div>
          <div class="lfp2_btn_n" @click="book_low(false)">
            继续预订原航班<br />No,Thank you
          </div>
        </div>
      </van-popup>
    </div>
    <!-- 经济舱高价位弹出层over -->

    <!-- 行李限制信息弹出 -->
    <div class="pup_cost" v-if="luggageList.length > 0">
      <van-popup v-model:show="luggageShow" round position="bottom">
        <div class="pup_cost_title" style="height: 15%">
          行李限制/Baggage limits
