    setRQ (data1, data2) {
      let CltId = this.booker.CltId;
      let allow_stops_0 = 0;
      var Segments = [];
      for (var i = 0; i < data1.length; i++) {
        var d1 = data1[i].date.split("-");
        var Segment = {
          Origin: [
            {
              Code: data1[i].from.CITY_CODE,
              Type: "CITY",
            },
          ],
          Destination: [
            {
              Code: data1[i].to.CITY_CODE,
              Type: "CITY",
            },
          ],
          DepartureDate: {
            Day: parseInt(d1[2]),
            Month: parseInt(d1[1]),
            Year: parseInt(d1[0]),
          },
          ConnectionLocations: [],
        };
        Segments.push(Segment);
        if (data1[i].back_date) {
          console.log(true, data1[i].back_date);
          var d2 = data1[i].back_date.split("-");
          var Segment = {
            Origin: [
              {
                Code: data1[i].to.CITY_CODE,
                Type: "CITY",
              },
            ],
            Destination: [
              {
                Code: data1[i].from.CITY_CODE,
                Type: "CITY",
              },
            ],
            DepartureDate: {
              Day: parseInt(d2[2]),
              Month: parseInt(d2[1]),
              Year: parseInt(d2[0]),
            },
            ConnectionLocations: [],
          };
          Segments.push(Segment);
        }
      }

      var json = {
        SeachEngine: {
          IBEPlus: true,
