    getTime (data) {
      let YY = data.getFullYear();
      let MM = data.getMonth() + 1;
      let DD = data.getDate();
      let HH = data.getHours();
      let Min = data.getMinutes();
      let SS = data.getSeconds();

      console.log(YY + "-" + MM + "-" + DD + HH + ":" + Min + ":" + SS);
      return YY + "-" + MM + "-" + DD + " " + HH + ":" + Min + ":" + SS;
    },

    toMoney (num) {
      num = parseFloat(num);
      //console.log(num)
      //num = num.toFixed(2);
      //console.log(num)
      num = num + "";

      //console.log(num)
      if (num.indexOf(".") > 0) {
        var x = num.split(".")[1];
        var y = num.split(".")[0];
        if (x.length >= 2) {
          return y + "." + x.substr(0, 2);
        } else if (x.length == 1) {
          return num + "0";
        } else if (x.length == 0) {
          return num + "00";
        }
      } else {
        return num + ".00"; //返回的是字符串23,245.12保留2位小数
      }
    },
