    async get_budget_left (allpx, allstr) {
      //进行提交的接口
      // this.upload();
      await axios({
        method: "post",
        url: "https://ezway.efasco.com/ErpService.asmx/SaveUNIQTrips",
        contentType: "application/x-www-form-urlencoded;charset=UTF-8",
        dataType: "json",
        data: allstr,
      }).then((res) => {
        this.Traveler_popup = false;
        this.successBookShow = true;
        if (res.data.toString().indexOf("error") < 0) {
          console.log("1");
          console.log(res.data);

          allstr = JSON.stringify(res.data);
          if (res.data.tktdeadline) {
            this.sussTime = res.data.tktdeadline;
            this.submt_info = res.data;
            // this.submt_pd = true;
            sessionStorage.removeItem("choose_flight");
            // sessionStorage.removeItem("search_flight");
          } else {
            console.log("2");
            console.log("预订完成");
          }
        } else if (res.data.toString().indexOf("预订失败") < 0) {
          console.log("3");
          this.successBookShow = false;

          this.dialogMseeage(
            "抱歉，您选择的折扣舱等已售罄，请尝试更换折扣舱等再行预订。Sorry, your chosen ticket is not available for this discount rate now,please try to change the discount class level and book again."
          );
          // this.submt_xx = "";
          // this.submt_pd = false;
        } else {
          console.log("4");
          this.successBookShow = false;
          this.dialogMseeage(
            "抱歉，您选择的折扣舱等已售罄，请尝试更换折扣舱等再行预订。Sorry, your chosen ticket is not available for this discount rate now,please try to change the discount class level and book again."
          );

          // this.submt_pd = false;
          // this.submt_xx = "";
        }
        sessionStorage.removeItem("getlist_" + this.now_trip);
      });
      // this.$toast.clear();
    },
    getJsonObj (pax, index) {
      let a = this.$store.state.CltId_set;
      let trip = {};

      trip.seqno = index + 1;
      trip.clientname = pax.DeptName;
      trip.bookname = this.booker.FullNameChs;
      trip.paxname = pax.FltPaxnameDom;
      trip.staffno = pax.StaffNo;
      trip.routetype = "D";
      trip.jdeno = this.booker.StaffNo;
      trip.mobileno = pax.MobileNo1;
      trip.IdNo = crypto.decrypt(pax.IdNo);
      // trip.IdNo = "31011519821026192X";

      trip.idtype = pax.idtype;
      // trip.idtype = "身份证";

      trip.profileid = pax.Identification;
      trip.trips = [];
      let seglist = this.choose_flight;

      let depdate = this.date_i(seglist[0].seg.segments[0].depdate);
      let arrdate = this.date_i(seglist[0].seg.segments[0].arridate);
      // console.log(seglist[0].seg.Segments[0].Depdate);
      // console.log(depdate)
      seglist.map((v, i) => {
        let seg_t = v.seg.segments[0];
        console.log(seg_t);
        let cabin_t = v.cabin;
        let cabin = {};
        let lowest_P =
          parseFloat(cabin_t.singlePrice) + parseFloat(cabin_t.airportTax);
        let lowest_info = v.seg.lowest_info || "";
        let dept = this.date_i(seg_t.depdate);
        let arrt = this.date_i(seg_t.arridate);
        if (dept < depdate) {
          depdate = dept;
        }
        if (arrt > arrdate) {
          arrdate = arrt;
        }
        cabin.flightno = seg_t.airline;
        cabin.depdate = new Date(dept).format("yyyy-MM-dd");
        cabin.deptime = seg_t.deptime;
        cabin.depairpot = seg_t.orgpotname;
        cabin.depcity = seg_t.orgcityname;
        cabin.arrdate = new Date(arrt).format("yyyy-MM-dd");
        cabin.arrtime = seg_t.arritime2;
        cabin.arrairpot = seg_t.dstpotname;
        cabin.arrairpotcode = seg_t.dstcity;
        cabin.depairpotcode = seg_t.orgcity;
        cabin.arrcity = seg_t.dstcityname;
        cabin.cabinlevel = cabin_t.cabinname;
        cabin.cabin = cabin_t.cabinDesc == "P" ? "Y" : cabin_t.cabinDesc;
        cabin.arrtem = seg_t.dstTerminal;
        cabin.deptem = seg_t.orgTerminal;
        cabin.cabindetail = cabin_t.cabinType;
        cabin.fb = cabin_t.farebase;
        cabin.tourcode = cabin_t.contractCode;
        cabin.discount = cabin_t.discountRate;
        cabin.price =
          parseFloat(cabin_t.singlePrice) + parseFloat(cabin_t.airportTax);
        cabin.lowestprice = lowest_P;
        //parseInt(xs.cabins[xs.cabins.length-1].SinglePrice) + parseInt(xs.cabins[xs.cabins.length-1].AirportTax)
        cabin.lowestinfo = lowest_info;
        //
        cabin.rccode = "";
        if (a.f_need_rc) {
          if (a.rc_by_discount && a.rc_by_discount < cabin_t.discountRate) {
            cabin.rccode = this.travel_setx[1].value;
          }
        }

        cabin.dtRcRemark = ""; //segs[i].sevendayrc
        cabin.isChangeable = cabin_t.isChangeable;
        cabin.isRefundable = cabin_t.isRefundable;
        cabin.isEndorsable = cabin_t.isEndorsable;
        cabin.yprice = seg_t.yprice;
        cabin.bookclasslevel = this.booker.ServiceLevel || "Y";
        cabin.tax = cabin_t.airportTax;
        cabin.pnr = seg_t.pnr;

        cabin.mileage = seg_t.tpm;
        cabin.duration = seg_t.duration;

        cabin.cccode = this.travel_setx_json.cccode || ""; //segs[i].cc.sel_cccode
        cabin.ccid = ""; //segs[i].cc.sel_ccid
        cabin.Baggage = cabin_t.baggage;
        cabin.inorderid = ""; //segs[i].cc.sel_inid
        cabin.inordercode = ""; //segs[i].cc.sel_incode
        if (cabin_t.cabinDesc == "Y") {
          // cabin.highestfare = seg_t.max_y;
        }
        if (cabin_t.cabinDesc == "F") {
          // cabin.highestfare = seg_t.max_f;
        }
        if (cabin_t.cabinDesc == "C") {
          // cabin.highestfare = seg_t.max_c;
        }
        cabin.NonCorpFare = cabin_t.nonCorpFare;
        cabin.highestfare = cabin_t.highestFare;
        cabin.Yprice = seg_t.yprice;

        cabin.lowestprice = cabin_t.lowestFare;
        // if(segs[i].lowest_cabin){
        //     let l_c =segs[i].lowest_cabin
        //     cabin.lowestinfo =  seg_t.Airline+'/'+l_c.CabinType+'舱'+'/'+(l_c.SinglePrice*1+l_c.AirportTax);
        // }
        trip.trips.push(cabin);
      });
      trip.depdate = new Date(depdate).format("yyyy-MM-dd");
      trip.arrdate = new Date(arrdate).format("yyyy-MM-dd");
      trip.paymentmethod = this.paymenValueObj.chtext;
      return trip;

      // console.log(pax)
      // return trip;
    },
    async set_submit_data_int () {
      //提交接口数据
      let a = this.$store.state.CltId_set;
      console.log(a);
      // let uniq_orderid = new GUID().newGUID();
      let guid = new GUID();
      let oi = guid.newGUID();
      let uniq_orderid = oi;
      // console.log(guid);
      // let staffid="RS_777-EC"
      let staffid = this.booker.StaffNo;
      let name = this.booker.FltPaxnameDom;
      let jst = {};
      let result = {
        tktdeadline: "2018-05-29 18:00:00",
        timespan: uniq_orderid,
        source: "app",
        list: [],
        travelpurpose: this.purposeValueObj.text,
        paymentmethod: this.paymenValueObj.chtext,
      };
      // this.travel_setx.map((vv) => {
      //   jst[vv.pointer] = vv.value;
      //   if (vv.pointer == "travelpurpose2" && a.need_travelpurpose2)
      //     result.travelpurpose2 = vv.value;
      //   if (vv.pointer == "travelpurpose") result.travelpurpose = vv.value;
      //   if (vv.pointer == "paymentmethod") result.paymentmethod = vv.value;
      //   if (vv.pointer == "accountbook") result.accountbook = vv.value;
      // });
      this.travel_setx_json = jst;

      this.selectpl_x.content.map((v, i) => {
        console.log(v);
        result.list.push(this.getJsonObj_int(v, i));
        // let trip=this.getJsonObj(v,)
      });
      console.log("result_int", result);

      // console.log(result);
      let allstr = JSON.stringify(result);

      sessionStorage.setItem("SUBMT_COM", JSON.stringify(result));

      let allpx = 0;
      let g = result.list;
      g.map(function (c, m) {
        let p = c.trips;
        p.map(function (v, i) {
          allpx += v.price * 1;
        });
      });

      this.get_budget_left_int(allpx, allstr);
    },
    async get_budget_left_int (allpx, allstr) {
      await axios({
        method: "POST",
        url: "https://ezway.efasco.com/ErpService.asmx/SaveUNIQIntTrips",
        data: allstr,
      }).then((res) => {
        // this.$toast.clear();

        if (res.data.toString().indexOf("error") < 0) {
          // console.log(res.data);

          allstr = JSON.stringify(res.data);
          this.submt_info = res.data;
          let data = this.submt_info;
          this.Traveler_popup = false;
          showDialog({
            title: "提示/Notice",
            message:
              "订单已生成，请返回出差申请单中提交审批/The order has been generated, please back to the travel request and submit for approval.",
            theme: "round-button",
            confirmButtonText: "确定/Confirm", //改变确认按钮上显示的文字
            confirmButtonColor:
              "linear-gradient(160deg, #0274c9 0%, #044db5 100%)", //确定按钮文字
          }).then((result) => {
            sessionStorage.setItem(
              "submt_info",
              JSON.stringify(this.submt_info)
            );
            console.log("订单生成接口数据", data);
            this.$router.push({
              path: "/applicationdetail",
              query: {
                StaffNo: data.list[0].jdeno,
                orderid: data.timespan,
                Status2: 1,
                auth: 0,
                formorder: true,
                // paymentmethod:,
                paymentmethod: this.paymenValueObj.chtext,
              },
            });
          });
        } else if (res.data.toString().indexOf("预订失败") < 0) {
          console.log("3");
          this.Traveler_popup = false;

          this.dialogErro();
        } else {
          console.log("4");
          this.Traveler_popup = false;

          this.dialogErro();
        }
      });
    },
    getJsonObj_int (pax, index) {
      //
      let a = this.$store.state.CltId_set;
      let trip = {};

      trip.seqno = index + 1;
      trip.clientname = pax.DeptName;
      trip.bookname = this.booker.FullNameChs;
      trip.paxname = pax.FltPaxnameDom;
      trip.staffno = pax.StaffNo;
      trip.routetype = "D";
      trip.jdeno = this.booker.StaffNo;
      trip.mobileno = pax.MobileNo1;
      trip.IdNo = crypto.decrypt(pax.IdNo);
      trip.profileid = pax.Identification;

      let info = this.info;
      trip.idtype = pax.idtype;

      trip.trips = [];
      console.log("this.info", info);
      if (info.length > 1) {
        trip.type = "多程/Multi Segments";

        trip.arrdate = info[info.length - 1].data;
        trip.depdate = info[0].date;
      } else if (info[0].back_date) {
        trip.depdate = info[0].date;
        trip.arrdate = info[0].back_date;
        trip.type = "往返/Round Trip";
      } else {
        trip.type = "单程/One Way";
        trip.depdate = info[0].date;
        trip.arrdate = info[0].date;
      }

      let segs = this.search.times == 1 ? [{}, {}] : [{}];
      segs[0].this_data = this.choose_flight[0].data;
      segs[0].this_data_cabin = this.choose_flight[0].cabin;
      segs[0].has_rc = false;
      if (this.search.times == 1) {
        //国际往返接口参数
        // console.log(this.choose_flight);
        segs[1].this_data = this.choose_flight[1].data;
        segs[1].this_data_cabin = this.choose_flight[1].cabin;
        segs[1].has_rc = false;
      } else {
        // console.log(this.choose_flight);
      }

      // console.log(segs);
      let index_n = 1;
      for (let i = 0; i < segs.length; i++) {
        let seg_se = segs[i].this_data.Sectors[0]; //当前点击航次;
        let seg_t_de = segs[i].this_data_cabin.SegmentCabins; //当前点击航次所有中转行程;
        let flight_arr = seg_se.FlightDescr.split("+"); //中转行程数组;
        // console.log(seg_se,seg_t_de,flight_arr)
        console.log("seg_se", seg_se);
        for (let j = 0; j < seg_se.Segments.length; j++) {
          let seg_t = seg_se.Segments[j]; //当前循环到的中转行程

          let cabin = {};
          cabin.flightno = flight_arr[j];
          cabin.flightnoReal = seg_t.AirlineCodeReal + seg_t.FlightNoReal;

          cabin.mileage = seg_t.Mileage;
          cabin.mealService = seg_t.MealService;

          cabin.aircraftCode = seg_t.AircraftCode;

          cabin.duration = seg_t.Duration;
          cabin.baggage =
            seg_t_de[j].BaggageType + "-" + seg_t_de[j].BaggageValue;
          // var setnewdate1 = setnewdate(seg_t.DestDate);
          // var setnewdate2 = setnewdate(seg_t.OrigDate);
          // var day1 = new Date(setnewdate1).format("yyyy-MM-dd");
          // var day2 = new Date(setnewdate2).format("yyyy-MM-dd");
          let day1 = seg_t.DestDateC;
          let day2 = seg_t.OrigDateC;
          cabin.depdate = day2;

          cabin.deptime = seg_t.OrigTime2;
          cabin.depairpot = seg_t.OrigAPNameCN;
          cabin.depairpot_en = seg_t.OrigAPNameEN;
          cabin.depairpotcode = seg_t.OrigAP;
          cabin.depcity = seg_t.orgcityname;
          cabin.arrdate = day1;
          cabin.arrtime = seg_t.DestTime2;
          cabin.arrairpot = seg_t.DestAPNameCN;
          cabin.arrairpot_en = seg_t.DestAPNameEN;
          cabin.arrairpotcode = seg_t.DestAP;
          cabin.arrcity = seg_t.dstcityname;
          cabin.cabin =
            seg_t_de[j].CabinDesc == "P" ? "Y" : seg_t_de[j].CabinDesc;
          cabin.cabindetail = seg_t_de[j].CabinType;
          cabin.cabinlevel = seg_t_de[j].CabinName;
          cabin.arrtem = seg_t.DestT;
          cabin.deptem = seg_t.OrigT;
          cabin.fb = seg_t_de[j].FareBasis;
          cabin.tripIndex = i + 1;
          cabin.index = index_n; //2*i+(j+1);
          index_n++;
          cabin.SplitAmount = seg_t_de[j].SplitAmount;
          cabin.price = segs[i].this_data_cabin.TotalAmt;
          cabin.tax = segs[i].this_data_cabin.TaxAmt;
          cabin.tourcode = segs[i].this_data_cabin.TourCode;
          if (segs[i].has_rc) {
            cabin.rccode = segs[i].rc;
            if (segs[i].has_rc == "more") {
              //
            } else {
              // console.log('123');
              let fl = segs[i].lowest_c;

              cabin.lowestprice = fl.LowestFare;
              let fl_l = fl.Sectors[0].FlightDescr.split("+");

              let fl_f = fl.Sectors[0].Segments;
              let low_info = "";
              for (let y = 0; y < fl_f.length; y++) {
                if (y != 0) {
                  low_info += "+";
                }
                low_info += fl_f[y].AirlineCode + fl_f[y].FlightNo;
                let ca = fl.Fares[0].SegmentCabins[y];
                low_info += ca.CabinName + "" + ca.CabinType;
              }
              cabin.lowestinfo = low_info;
            }
          } else {
            //
            cabin.lowestprice = cabin.price;
            cabin.lowestinfo =
              flight_arr[0] +
              segs[0].this_data_cabin.SegmentCabins[0].CabinName +
              segs[0].this_data_cabin.SegmentCabins[0].CabinType;
          }
          cabin.rccode = "";
          // console.log("a",a)
          // if (a.f_need_rc) {//航班原因代码
          //   if (a.rc_by_discount && a.rc_by_discount < cabin_t.discountRate) {
          //     cabin.rccode = this.travel_setx[1].value;
          //   }
          // }
          if (a.notneedint) {
            //航班原因代码
            if (this.reason_f_need_rc) {
              if (JSON.stringify(this.reasonObj) == "{}") {
                cabin.rccode =
                  "AR-起降机场,时间不合适/Landing airport,time is not suitable";
              } else {
                cabin.rccode =
                  this.reasonObj.code +
                  "-" +
                  this.reasonObj.details +
                  this.reasonObj.detailsEn;
                // console.log(true, this.reasonObj);
              }
            } else {
              cabin.rccode = "";
            }
          }

          cabin.dtRcRemark = segs[i].lessthan7;

          // console.log(segs, seg_t);

          trip.trips.push(cabin);
        }

        let new_j = {};
      }
      trip.price = segs[segs.length - 1].this_data_cabin.TotalAmt;
      trip.tax = segs[segs.length - 1].this_data_cabin.TaxAmt;

      return trip;
    },
    date_i (a) {
      if (isNaN(a * 1)) {
        a = a.replace(/-/g, "/");
      } else {
        a = a * 1;
      }
      return new Date(a).getTime();
    },
    getQueryString (name) {
      let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      let urlse = decodeURI(location.search);
      let r = urlse.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      return null; // 你这个一个页面写那么多 其他页面调用这里的是没 没是的 这个页面当两个页面写了
    },
    //判断URL协议方案是https还是http
    getBaseUrl () {
      var ishttps = "https:" == location.protocol ? true : false;
      return ishttps;
    },
    dialogMseeage (message) {
      showDialog({
        title: "提示/Notice",
        message: message,
        theme: "round-button",
        confirmButtonText: "确定/Confirm", //改变确认按钮上显示的文字
        confirmButtonColor: "linear-gradient(160deg, #0274c9 0%, #044db5 100%)", //确定按钮文字
      });
    },
    dialogErro () {
      showDialog({
        title: "提示/Notice",
        message:
          "抱歉，您选择的折扣舱等已售罄，请尝试更换折扣舱等再行预订。Sorry, your chosen ticket is not available for this discount rate now,please try to change the discount class level and book again.",
        theme: "round-button",
        confirmButtonText: "确定/Confirm",
        confirmButtonColor: "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
      });
    },
  },
  components: { ScrollTop },
};
</script>
