<template>
  <!-- btnFlag 控制显示隐藏 -->
  <!-- backTop 回顶部的方法 -->

  <div>
    <transition name="scro">
      <div class="go_top" v-if="btnFlag" @click="backTop">
        <img src="../../assets/image/to_scro.png" alt="" width="20" height="20" />
      </div>
    </transition>
  </div>
  <!--  -->
</template>
<script>
export default {
  data () {
    return {
      btnFlag: false,
      scrollTop: 0,
    };
  },
  mounted () {
    //挂载完成
    window.addEventListener("scroll", this.scrollToTop);
  },

  destroyed () {
    //销毁后
    window.removeEventListener("scroll", this.scrollToTop);
  },
  methods: {/*  */
    // 点击回到顶部方法，加计时器过渡顺滑
    backTop () {
      const that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },

    // 计算距离顶部的高度，当高度大于100显示回顶部图标，小于100则隐藏
    scrollToTop () {
      const that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      that.scrollTop = scrollTop;
      if (that.scrollTop > 100) {
        that.btnFlag = true;
      } else {
        that.btnFlag = false;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.go_top {
  position: fixed;
  z-index: 99;
  bottom: 100px;
  right: 20px;
  border-radius: 50%;
  background-color: #ffffff00;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #bfbfbf;
}

.scro-enter-active {
  animation: fadeIn 0.9s;
}

.scro-leave-active {
  animation: fadeOut 0.5s;
}
</style>