import axios from "../utils/axiosLogin.js"
import CryptoJS from "../utils/encryp.js"
export const Get_login = (userName, passWord) => {
  const username = CryptoJS.encrypt(userName, "abcdefgabcdefg12").toString()
  const password = CryptoJS.encrypt(passWord, "abcdefgabcdefg12").toString()
  return axios.post(
    "ErpService.asmx/LoginUNIQByPost",
    { username, password },
    //设置请求头
    {
      headers: {
        "Content-Type": "text/json",
      },
    }
  )
}
export const Get_Change_Password = (username, oldpassword, newpassword) => {
  // encodeURIComponent 用法   在编码过程中   非字母数字字符 被替换成 ~%~ 符号 后跟两位十六进制数  二字母数字保持不变。
  const userName = encodeURIComponent(username)
  const oldpw = encodeURIComponent(oldpassword)
  const newpw = encodeURIComponent(newpassword)
  return axios.get("ErpService.asmx/ChangePasswd", {
    params: {
      username: userName,
      oldpw,
      newpw,
    },
  })
}

export const Get_Platform_Config = (CltId) => {
  return axios.post("ErpService.asmx/GetPlatformConfig", {
    cltid: CltId,
  })
}
