<template>
  <div class="loginBg" v-if="Mobile">
    <div class="loginTitle">
      <h3 v-if="changeTitle">
        <span v-for="(char, index) in loginText" :key="index" :class="{ 'falling': char.visible }">{{ char.value }}</span>
      </h3>
      <h3 v-else class="loginChangePassword">
        修改密码/Change Password
      </h3>
    </div>
    <van-form class="loginUsernameAndUserPassword">
      <van-cell-group inset>
        <!-- <van-field v-model="username" name="用户名" placeholder="请输入用户名/Username" :rules="[{ required: true, message: '请填写用户名/Please fill in the username' }]" /> -->
        <van-field v-model="username" name="用户名" placeholder="请输入用户名/Username" />
        <van-field v-model="password" type="password" name="密码" placeholder="请输入密码/Password" v-if="changeTitle" />
        <van-field v-model="OldPassword" type="Password" name="密码" placeholder="请输入原密码/Old Password" v-if="!changeTitle" />
        <van-field v-model="NewPassword" type="password" name="密码" placeholder="请输入新密码/New Password" v-if="!changeTitle" />
        <van-field v-model="ConfirmNewPassword" type="password" name="密码" placeholder="请确认新密码/Confirm new Password" v-if="!changeTitle" />
      </van-cell-group>
      <van-checkbox v-model="loginChecked" shape="square" class="loginRadio" v-if="changeTitle">保存密码/Save Password</van-checkbox>
      <div style="margin: 16px;">
        <van-button round block type="primary" @click="onSubmit" v-if="changeTitle">
          登录 / Log In
        </van-button>
        <van-button round block type="primary" @click="confirmOnsubmit" v-else>
          确认修改/Confilm
        </van-button>
        <span class="changPasswordbutton" @click="loginChangePassword" v-if="changeTitle">修改密码 <br> Modify Password</span>
        <span class="changPasswordbutton" @click="loginChangePassword" v-else>取消 <br> Cancel</span>

      </div>
    </van-form>
    <!-- 条款 -->
    <div class="loginClause" v-if="changeTitle">
      <div class="loginClauseLeft">
        <van-checkbox v-model="loginClause" shape="square" class="loginRadio" icon-size="15px">我已阅读并同意 </van-checkbox>
        <span class="loginPrivacyPolicy" @click="loginPrivacyPolicy">《隐私条款》</span>
      </div>

      <div class="loginCallUs">
        <van-icon name="phone-o" class="loginCallicon" />
        <span>
          <a href="tel:021962990">在线呼叫 Call Us</a>
        </span>
      </div>
    </div>
  </div>
  <error-network v-else></error-network>
</template>

<script>
import { Get_login, Get_Change_Password } from "../../api/loginApi.js";
import { showNotify } from 'vant';
import { setCookie, getCookie, deleteCookie } from "../../utils/cookie.js";
import { mapState } from 'vuex';
import errorNetwork from "../errorPage/errorNetwork.vue";
export default {
  name: 'login',
  data () {
    return {
      username: localStorage.getItem('userName'),
      password: '',
      OldPassword: '',
      NewPassword: '',
      ConfirmNewPassword: '',
      // 保存密码
      loginChecked: false,
      loginClause: false,
      // 隐私条款
      changeTitle: true,
      agreement: true,
      // 客户个人信息
      loginUserInformation: {},
      loginText: [
        { value: 'F', visible: false },
        { value: 'A', visible: false },
        { value: 'S', visible: false },
        { value: 'C', visible: false },
        { value: 'O', visible: false },
        { value: '企', visible: false },
        { value: '业', visible: false },
        { value: '费', visible: false },
        { value: '控', visible: false },
        { value: '系', visible: false },
        { value: '统', visible: false },
      ],
    }
  },
  computed: {
    ...mapState(['Mobile'])
  },
  components: {
    errorNetwork
  },
  created () {
    if (localStorage.getItem('password')) {
      // this.username = localStorage.getItem('userName')
      this.loginChecked = true
      this.password = localStorage.getItem('password')
    }
  },
  mounted () {
    this.animateText();
  },
  methods: {
    async animateText () {
      for (let i = 0; i < this.loginText.length; i++) {
        this.loginText[i].visible = true;
        await this.$nextTick();
        await this.sleep(200); // 控制每个字的间隔时间，可以根据需要调整
      }
    },
    sleep (ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    },
    loginChangePassword () {
      this.animateText();
      this.changeTitle = !this.changeTitle
    },
    // 隐私条款
    loginPrivacyPolicy () {
      this.$router.push('/privacy')
    },
    async onSubmit (value) {
      // 加载调用
      // this.showLoading()
      if (!this.username || !this.password) {
        // 危险通知
        showNotify({ type: 'danger', message: '登录信息不正确 / Invalid Login Information' });
        return
      }
      if (!this.loginClause) {
        showNotify({ type: 'danger', message: '请确认阅读隐私条款' });
        return
      }
      await Get_login(this.username, this.password).then((res) => {

        console.log(res);
        if (res.status == 200 && typeof res.data == 'object') {
          // this.loginUserInformation = JSON.parse(res.data)
          if (this.password == "777") {
            this.changeTitle = false
            showNotify({ type: 'danger', message: '请先修改初始密码Please change the initial password first' });
            return
          }
          // 保存密码
          if (this.loginChecked) {
            localStorage.setItem('password', this.password)
          } else {
            localStorage.removeItem('password', this.password)
          }
          sessionStorage.setItem("agreement", this.agreement); //登录重定向
          // 配置公共资源
          this.$puilc.set_cltid(res.data.CltId)
          // 客户信息
          sessionStorage.setItem("booker", JSON.stringify(res.data))
          // staffno 
          sessionStorage.setItem("StaffNo", res.data.StaffNo)
          // 用户名
          localStorage.setItem('userName', this.username)
          // 存取用户中文名
          localStorage.setItem('getUserNameChinese', res.data.FltPaxnameDom)
          //  0表示过期时间 关闭浏览器时失效
          setCookie('saffid', res.data.StaffNo, 0)
          this.$router.push('/index')
        } else if (typeof res.data == 'string') {
          showNotify({ type: 'danger', message: `${res.data}` });
        }
      })
    },
    confirmOnsubmit () {
      if (!this.username) {
        showNotify({ type: 'danger', message: '请输入用户名！' });
        return
      }
      if (!this.OldPassword) {
        showNotify({ type: 'danger', message: '请输入原密码！' });
        return
      }
      if (!this.NewPassword) {
        showNotify({ type: 'danger', message: '请输入新密码！' });
        return
      }
      if (!this.ConfirmNewPassword) {
        showNotify({ type: 'danger', message: '请再次输入新密码！' });
        return
      }
      if (this.NewPassword.length < 8) {
        showNotify({ type: 'danger', message: '新密码至少需要8位字符！' });
        return
      }
      if (this.NewPassword !== this.ConfirmNewPassword) {
        showNotify({ type: 'danger', message: '您两次输入的密码不一致！' });
        return
      }
      this.changePassword(this.username, this.OldPassword, this.NewPassword)
    },
    // 修改密码逻辑
    async changePassword (userName, oldPassword, newPassword) {
      await Get_Change_Password(userName, oldPassword, newPassword).then((res) => {
        if (res && res.data) {
          if (res.data != "success") {
            showNotify({ type: 'danger', message: `${res.data}` });
            return
          } else {
            showNotify({ type: 'success', message: '修改成功！' });
            this.OldPassword = ''
            this.NewPassword = ''
            this.ConfirmNewPassword = ''
            this.changeTitle = true
            return
          }
        }
      })
    }
  }
}
</script>

<style lang="scss">
.loginBg {
  height: 100%;
  background: url("../../assets/img/loginBg2.jpg") no-repeat;
  background-size: contain;
  position: relative;
  .loginTitle {
    position: absolute;
    top: 32%;
    left: 34%;
    transform: translate(-50%, -50%);
    color: #0066cc;
    text-align: left;
    font-weight: 900;
    padding-left: 44px;
    font-size: 24px;
    white-space: nowrap;
    h3 {
      font-size: 24px;
      margin: 0;
      overflow: hidden;
      white-space: nowrap;
      span.falling {
        display: inline-block;
        opacity: 0;
        transform: translateY(-50px);
        animation: dropDown 0.5s ease-in-out forwards;
      }
    }
    .loginChangePassword {
      margin-left: 80px;
    }
    @keyframes dropDown {
      to {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
  .loginUsernameAndUserPassword {
    width: 90%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    margin-top: 30px;
    // .van-cell-group {
    // color: transparent;
    .van-cell-group--inset {
      .van-cell {
        padding: 10px 10px;
        width: 100%;
        font-size: 13px;
        border-radius: 25px;
        background-color: #eeeeee;
        margin-bottom: 10px;
        .van-cell__value {
          .van-field__control::placeholder {
            color: black;
            opacity: 0.5;
          }
        }
      }
    }
    .loginRadio {
      margin-left: 20px;
    }
    .changPasswordbutton {
      float: right;
      cursor: pointer;
      font-size: 12px;
      color: #666;
      text-align: center;
      margin-top: 10px;
    }
  }
  .loginClause {
    width: 100%;
    position: absolute;
    bottom: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    .loginClauseLeft {
      display: flex;
      align-items: center;
      .loginPrivacyPolicy {
        color: #45c7ee;
      }
      .van-checkbox__icon {
        margin-bottom: 2px;
      }
    }

    .loginCallUs {
      color: #337ab7;
      font-size: 14px;
      span {
        a {
          color: #337ab7;
        }
        margin-left: 5px;
      }
    }
  }
}
</style>