                        <van-field class="from_i_field" v-model="Guarantee_card.Number" name="卡号" label="卡号" placeholder="卡号" />
                      </div>
                      <div class="from_item">
                        <van-field class="from_i_field" v-model="Guarantee_card.CVV" name="验证码" label="验证码" placeholder="验证码" />
                      </div>
