      <van-picker show-toolbar allow-html confirm-button-text="确认/confirm" cancel-button-text="取消/cancel" :columns="get_this_list(idtype_list, idtype_se)" @confirm="choose_idt" @cancel="idtype_s = false" />
    </van-popup>

    <ScrollTop></ScrollTop>
  </div>
</template>
<script>
import { GUID } from "../../utils/guid.js";
import Map from "../children/map.vue";
import travel_set from "../children/travel_set.vue";
import crypto from "../../utils/crypto.js";
import ScrollTop from "../scrolltopPage/ScrollTop.vue";
import axios from '../../utils/axiosOriginal.js'
import { showDialog } from 'vant';

// import { puilc2 } from "../../utils/puilc2.js";

export default {
  data () {
    let w = window.innerWidth;
    let h = window.innerHeight;
    return {
      Show: false,
      styleObject: {
        width: w,
        bgTM: -1 * 0.3 * w,

        marginTop: 0.5 * w,
        innerHeight: h - 0.5 * w,

        scrollable: h - 0.5 * w - 70,
      },
      PaymentType: null, //预付款或担保

      search_hotel: JSON.parse(sessionStorage.getItem("search_hotel")),
      booker: JSON.parse(sessionStorage.getItem("booker")),
      hotelData: {},
      hotel_Det: {},
      isOpen: -1,
      mapShow: false,
      mapData: [{}],
      successBookShow: false,
      text: "",
      room: null,
      Traveler_popup: false,
      choose_hotel: null,
      purposeValue: 0,
      purposeOptions: [
        // 出差旅费Travelling Expense
        { text: "Select...", chtext: "请选择", value: 0 },
        { text: "商务差旅/Business travel", chtext: "商务差旅", value: 1 },
        {
          text: "公司福利休假/Company welfare leave",
          chtext: "公司福利休假",
          value: 2,
        },
        { text: "拜访客户/Visiting customers", chtext: "拜访客户", value: 3 },
      ],
      purposeValueObj: "",
      last_arrive: [
        {
          value: "Select...",
          not_need_search: true,
          // valuesearch: "",
          ctr: false,
          title: "最晚到店时间",
          columns: [
            {
              text: "当天",
              value: '当天',
              children: [
                { text: "14:30", value: "14:30" },
                { text: "15:00", value: "15:00" },
                { text: "15:30", value: "15:30" },
                { text: "16:00", value: "16:00" },
                { text: "16:30", value: "16:30" },
                { text: "17:00", value: "17:00" },
                { text: "17:30", value: "17:30" },
                { text: "18:00", value: "18:00" },
                { text: "18:30", value: "18:30" },
                { text: "19:00", value: "19:00" },
                { text: "19:30", value: "19:30" },
                { text: "20:00", value: "20:00" },
                { text: "20:30", value: "20:30" },
                { text: "21:00", value: "21:00" },
                { text: "21:30", value: "21:30" },
                { text: "22:00", value: "22:00" },
                { text: "22:30", value: "22:30" },
                { text: "23:00", value: "23:00" },
                { text: "23:30", value: "23:30" },
                { text: "23:59", value: "23:59" },
              ],
            },
            {
              text: "次日",
              value: "次日",
              children: [
                { text: "01:00", value: '01:00' },
                { text: "01:30", value: "01:30" },
                { text: "02:00", value: "02:00" },
                { text: "02:30", value: "02:30" },
                { text: "03:00", value: "03:00" },
                { text: "03:30", value: "03:30" },
                { text: "04:00", value: "04:00" },
                { text: "04:30", value: "04:30" },
                { text: "05:00", value: "05:00" },
                { text: "05:30", value: "05:30" },
                { text: "06:00", value: "06:00" },
              ],
            },
          ],
        },
      ],

      hac_o: 0,
      room_choose: [],
      cancel_r: [],
      // SelfPayShow: false,
      idtype_s: false,
      idtype_se: "",

      idtype_list: [
        "身份证",
        "护照",
        "大陆居民往来港澳通行证",
        "港澳居民来往内地通行证",
        "台湾居民往来大陆通行证",
        "港澳台居民居住证",
        "其他证件类型",
      ],

      Guarantee_card: sessionStorage.getItem("card")
        ? JSON.parse(sessionStorage.getItem("card"))
        : {},
      clti: JSON.parse(sessionStorage.getItem("set_cltid")),
      allday: 1,
      cancel_time: null,
    };
  },
  created () {
    this.getParams();
    console.log(this.hotelData);
    this.search_hotel.psg.map((v, i) => {
      let a = this.get_rule(v, i);

      //   console.log(a);
      a.then((vt) => {
        // console.log(vt);
        // v.limit = vt.data;
      });
    });

    // this.clti = this.puilc.set_cltid(this.booker.CltId);
    console.log(this.clti);
    this.get_hotel_detail();
    this.set_room();
    // console.log(returnCitySN)
  },
  mounted () {
    this.Show = false;
    this.$nextTick(() => {
      this.Show = true;
    });
    console.log(this.clti);
  },
  updated () {
    // this.rule_co(this.$refs.cancel_rule);
    if (this.$refs.cancel_rule) {
      for (let i = 0; i < this.$refs.cancel_rule.length; i++) {
        if (
          this.$refs.cancel_rule[i].outerText.indexOf("不允许变更取消") != -1
        ) {
          this.$refs.cancel_rule[i].style.color = "#EB6301";
        }
      }
    } else {
    }
  },
  methods: {
    ready_puilc2 () { },
    getParams () {
      const routerParams = JSON.parse(sessionStorage.getItem("hoteldata"));
      this.hotelData = routerParams
      // this.$set(this, "hotelData", routerParams);
      // console.log(this.ready_puilc2)
    },
    get_rule (v, i) {
      let vv = this.search_hotel.psg[i];
      let a = axios({
        method: "GET",
        url:
          "https://timas.efasco.com/hotelservice.asmx/getFXHotelIDByElongId?elongid=" +
          this.hotelData.HotelId,
        // method: "post",
        // url: "https://cwellsapi.casonline.cn/apiservice/apiservice.asmx/GetHotelPolicy",
        // data:
        //   "jobtitle=" +
        //   (v.JobTitle && v.JobTitle != "Y" ? v.JobTitle : "其他人员") +
        //   "&startdate=" +
        //   this.search_hotel.arrivalDate +
        //   "&enddate=" +
        //   this.search_hotel.departureDate +
        //   "&district=" +
        //   (this.hotelData.HotelInfo.Detail.District.substr(-4) || "0000") +
        //   "&city=" +
        //   (this.hotelData.HotelInfo.Detail.CityId ||
        //     this.search_hotel.cities[0].LocIdCity),
      });
      return a;
    },
    async get_hotel_detail () {
      if (this.cancel) {
        this.cancel();
      }
      let CancelToken = axios.CancelToken;
      let that = this;
      // this.upload();
      // var noroomtip = CltId_set.hotel_no_room_tip || "您查询的房型已满房";
      var noe_data = this.hotelData;
      var ready_ajax = [false, false];
      ready_ajax = {
        el: false,
        hrs: false,
        corp: false,
        corp2: false,
        fx: false,
        el_list: noe_data,
      };
      let a = await axios({
        method: "post",
        url: "https://timas.efasco.com/HotelService.asmx/getELONGHotelDetail", //'&currency=&language=ENG&customerkey='+gtaservice
        data: JSON.stringify({
          ArrivalDate: this.search_hotel.arrivalDate,
          DepartureDate: this.search_hotel.departureDate,
          HotelIds: this.hotelData.HotelId,
          PaymentType: "All",
          Options: "1,2,4",
        }),
        cancelToken: new CancelToken(function executor (c) {
          // // 这个参数 c 就是CancelToken构造函数里面自带的取消请求的函数
          that.cancel = c;
        }),
      }).catch(() => {
        showDialog({
          title: "提示/Notice",
          message:
            "请求失败，请检查您的网络/The request failed. Please check your network.",
          theme: "round-button",
          confirmButtonText: "确定/Confirm",
          confirmButtonColor:
            "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
        }).then(() => {
          this.$router.go(-1);
        });
      });
      console.log('9999', a);
      this.set_alld(
        this.search_hotel.arrivalDate,
        this.search_hotel.departureDate
      );
      if (a.data.Code.indexOf("酒店已下线") != -1) {
        // this.$toast.clear();
        showDialog({
          title: "提示/Notice",
          message:
            "您查询的酒店已下线/The hotel you inquired about has been offline.",
          theme: "round-button",
          confirmButtonText: "确定/Confirm",
          confirmButtonColor:
            "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
        }).then(() => {
          this.$router.go(-1);
        });
      }
      // this.$toast.clear()

      if (a.data.Result.Hotels[0].Rooms.length == 0) {
        // this.$toast.clear();
        showDialog({
          title: "提示/Notice",
          message: "您查询的房型已满房/The room type you inquired is full.",
          theme: "round-button",
          confirmButtonText: "确定/Confirm",
          confirmButtonColor:
            "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
        }).then(() => {
          this.$router.go(-1);
        });
      }

      if (!!a.data) {
        if (ready_ajax["corp2"]) {
          var jsonxy = JSON.parse(JSON.stringify(a.data));
          jsonxy.Result.Hotels[0] = ready_ajax["corp2"];
          jsonxy.Result.Hotels[0].Rooms = [];
          ready_ajax["corp2"] = jsonxy;
        }
        // console.log(ready_ajax["corp2"])
        var HotelRoomList_0 = a.data;
        if (a.data.Code != "0") {
          // ready_ajax["el"] = noroomtip;
          if (ready_ajax["el"] && ready_ajax["hrs"] && ready_ajax["fx"]) {
            // fn(noe_data, ready_ajax);
          }
          return;
        }
        HotelRoomList_0.Result.Hotels[0].Rooms =
          HotelRoomList_0.Result.Hotels[0].Rooms || [];
        var HotelRoomList = HotelRoomList_0.Result.Hotels[0].Rooms; //酒店列表各类房
        if ((!HotelRoomList || !HotelRoomList.length) && !ready_ajax["corp"]) {
          // ready_ajax["el"] = noroomtip;
          ready_ajax["el"] = "您查询的房型已满房";
          if (HotelRoomList_0.Result.Hotels[0].LowRate == 0) {
            // ready_ajax["el"] =
            //   CltId_set.hotel_no_room_tip || "暂无资源，请线下联系";
            ready_ajax["el"] = "暂无资源，请线下联系";
          }
          if (ready_ajax["el"] && ready_ajax["hrs"] && ready_ajax["fx"]) {
            // fn(noe_data, ready_ajax);
          }

          return;
        }
      }

      // this.set_click_PorG()

      // console.log("HotelRoomList",HotelRoomList)
      this.hotel_Det = a.data.Result.Hotels[0]
      // this.$set(this, "hotel_Det", a.data.Result.Hotels[0]);
      let hl_s = this.hotel_Det;
      var irr;
      for (let i = 0; i < hl_s.Rooms.length; i++) {
        var romms = hl_s.Rooms[i];
        romms.RatePlans.map((v, i) => {
          var PaymentType = v.PaymentType;
          this.onRuleX(PaymentType, v, hl_s);
          // this.hd_fx(v);
          this.get_TotalRate(v);
        });
      }
      // this.GetHotelDetail1(this.hotelData,)
      // console.log()

      if (a.data.Result.Hotels[0].Rooms.length == 0) {
        showDialog({
          title: "提示/Notice",
          message: "您查询的房型已满房/The room type you inquired is full.",
          theme: "round-button",
          confirmButtonText: "确定/Confirm",
          confirmButtonColor:
            "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
        }).then(() => {
          this.$router.go(-1);
        });
      }
      // this.hotel_Det.Rooms.map((v) => {
      //   this.itemTemplate(v, 0,this.hotel_Det,this.search_hotel,);
      // });
      if (this.PaymentType == "Prepay") {
        ready_ajax["hrs"] = { HotelRoomList: [] };
      }
      // console.log(ready_ajax)
    },
    get_TotalRate (irr) {
      //酒店价格
      //clti
      var payment = "";
      if (irr.PaymentType == "Prepay") payment = "预付/Prepay";
      if (irr.PaymentType == "SelfPay") payment = "前台现付/Postpay";
      if (!payment && this.booker.CltId == 1430) payment = "前台现付/Postpay";
      var discount = 1;
