    returnCancel_r (cancel_r, info, POG1, POG2, POG3, irr, hl_s) {
      let cancel_text = "",
        cancel_r2 = [],
        cancel_time = "";
      let aaa = new Date(
        info.checkin.replace(/-/g, "/") + " 24:00:00"
      ).getTime();
      if (cancel_r) {
        if (POG1 == "GuaranteeRuleIds") {
          //&&irr.PaymentType=='SelfPay'  //irr.cancel_text GuaranteeRuleIds
          // 担保规则取消变更规则：
          //         NoChange、不允许变更取消 (不管时间有没有超过  StartTime，下单后都不能取消)
          //         NeedSomeDay、允许变更/取消,需在XX日YY时之前通知
          //         NeedCheckin24hour、允许变更/取消,需在到店日期的24点之前几小时通知
          // console.log(irr)
          var g_rule = "";
          if (cancel_r.GuaranteeType == "FirstNightCost") {
            //FirstNightCost为首晚房费担保 FullNightCost为全额房费担保
            var t = "首晚房费";
          } else if (cancel_r.GuaranteeType == "FullNightCost") {
            var t = "全额房费";
          }
          if (cancel_r.IsTimeGuarantee == "false") {
            //False:为不校验到店时间True:为需要校验到店时间
          } else if (cancel_r.IsTimeGuarantee == "true") {
            var n = cancel_r.EndTime;
            if (n.length == 4) {
              n = "0" + n;
            }
            if (
              cancel_r.StartTime.substr(0, 2) * 1 >
              cancel_r.EndTime.substr(0, 2) * 1
            ) {
              n = "次日" + n;
            }
            g_rule =
              cancel_r.StartTime +
              "至" +
              n +
              "到店，需要您提供信用卡担保(担保金额为" +
              t +
              ")，";
          }
          if (cancel_r.IsAmountGuarantee == "false") {
            //False:为不校验房量条件 True:为校验房量条件 如果IsTimeGuarantee和IsAmountGuarantee都为false则为强制担保
            if (cancel_r.IsTimeGuarantee == "false") {
              g_rule = "需要您提供信用卡担保(担保金额为" + t + ")，";
            }
          }
          if (cancel_r.IsAmountGuarantee == "true") {
            var n = cancel_r.Amount;
            if (g_rule && g_rule.indexOf("需要您提供信用卡担保") != -1) {
              g_rule = g_rule.split("，")[0] + "，";
            }
            g_rule =
              (g_rule ? g_rule + "或" : "") +
              "预订" +
              n +
              "间房及以上，需要您提供信用卡担保(担保金额为" +
              t +
              ")，";
            // if (tar3) {
            //   set_num_changed(total_Price, cancel_r, n);
            // }
          }
          if (cancel_r.ChangeRule == "NoChange") {
            cancel_text = "不允许变更取消";
          }
          if (cancel_r.ChangeRule == "NeedSomeDay") {
            cancel_time =
              aaa -
              cancel_r.Day * (24 * 3600 * 1000) -
              cancel_r.Time * (3600 * 1000);
            // freecanceldeadl_hotel;
            cancel_text =
              "在(到店当日24点开始计算)" +
              cancel_r.Day +
              "天" +
              cancel_r.Time +
              "时之前可通知变更/取消";
          }
          if (cancel_r.ChangeRule == "NeedCheckin24hour") {
            var changertime = cancel_r.Hour;
            if (changertime > 24) {
              changertime =
                Math.floor(changertime / 24) + "天" + (changertime % 24);
            }
            cancel_time = aaa - cancel_r.Hour * (3600 * 1000);
            // - freecanceldeadl_hotel;
            cancel_text =
              "在(到店当日24点开始计算)" +
              changertime +
              "小时前可通知变更/取消";
          }
          cancel_text = g_rule + cancel_text;
          // console.log(cancel_text);
        }
        if (POG1 == "PrepayRuleIds") {
          // PrepayNoChange：不允许变更取消；
          //     PrepayNeedSomeDay：在到店当日24点前Hour小时前按规则看是否可以免费变更取消（一般是不收罚金），在Hour和Hour2之间按规则存在罚金，Hour2之后不能变更取消；
          //     PrepayNeedOneTime：在约定日期时间点(DateNum + Time)前可以免费变更取消
          if (cancel_r.ChangeRule == "PrepayNoChange") {
            cancel_text = "不允许变更取消";
          }
          if (cancel_r.ChangeRule == "PrepayNeedSomeDay") {
            var stage1_rule = "";
            if (cancel_r.DeductFeesBefore == 1) {
              //DeductFeesBefore为1表示扣费，0表示不扣费。用于 PrepayNeedSomeDay的Hour前扣款类型（一般不收罚金）。
              if (cancel_r.CashScaleFirstBefore == "Money") {
                //Money：金额  Percent：比例 FristNight：首晚
                stage1_rule =
                  "取消将收取罚金(" + cancel_r.DeductNumBefore + "元)";
              }
              if (cancel_r.CashScaleFirstBefore == "Percent") {
                stage1_rule =
                  "取消将收取罚金(全额房费的百分之" +
                  cancel_r.DeductNumBefore +
                  ")";
              }
              if (cancel_r.CashScaleFirstBefore == "FristNight") {
                stage1_rule = "取消将收取罚金(首晚房费)";
              }
            } else {
              stage1_rule = "可以免费变更取消";
            }
            var stage2_rule = "";
            if (cancel_r.DeductFeesAfter == 1) {
              //用于 PrepayNeedSomeDay的Hour到Hour2之间的扣款类型。DeductFeesAfter为1表示扣费，0表示不扣费。
              if (cancel_r.CashScaleFirstAfter == "Money") {
                //Money：金额  Percent：比例 FristNight：首晚
                stage2_rule =
                  "取消将收取罚金(" + cancel_r.DeductNumAfter + "元)";
              }
              if (cancel_r.CashScaleFirstAfter == "Percent") {
                stage2_rule =
                  "取消将收取罚金(全额房费的百分之" +
                  cancel_r.DeductNumAfter +
                  ")";
              }
              if (cancel_r.CashScaleFirstAfter == "FristNight") {
                stage2_rule = "取消将收取罚金(首晚房费)";
              }
            } else {
              stage2_rule = "可以免费变更取消";
            } //存在罚金
            var changertime = cancel_r.Hour;
            if (changertime > 24) {
              changertime =
                Math.floor(changertime / 24) + "天" + (changertime % 24);
            }
            var changertime2 = cancel_r.Hour2;
            if (changertime2 > 24) {
              changertime2 =
                Math.floor(changertime2 / 24) + "天" + (changertime2 % 24);
            }
            var ckin =
              info.checkin.replace(/-/g, "/") +
              (this.if_Safari() ? "T" : " ") +
              "23:59:59";
            var ckin3 = info.checkin.replace(/-/g, "/") + " 23:59:59";
            var ckin4 = ckin3.split(/[- / :]/);
            var ckin2 = ckin.replace(/-|:|\s|\//g, "");
            var nd_f = new Date().format("yyyyMMdd") + "000000";
            var ct3 =
              new Date(
                ckin4[0],
                ckin4[1] - 1,
                ckin4[2],
                ckin4[3],
                ckin4[4],
                ckin4[5]
              ).getTime() -
              cancel_r.Hour * 60 * 60 * 1000;
            cancel_text =
              "在(到店当日24点开始计算)" +
              changertime +
              "小时前" +
              stage1_rule +
              "，在" +
              changertime +
              "小时和" +
              changertime2 +
              "小时之间" +
              stage2_rule +
              "，" +
              changertime2 +
              "小时之后不能变更取消";
            // if(ckin2<nd_f){
            cancel_time = aaa - cancel_r.Hour * (3600 * 1000);
            // -freecanceldeadl_hotel;
            if (ct3 < new Date().getTime()) {
              cancel_text = "不允许变更取消";
            }
          }
          if (cancel_r.ChangeRule == "PrepayNeedOneTime") {
            cancel_time =
              new Date(cancel_r.Day).getTime() + cancel_r.Time * (3600 * 1000);
            // -freecanceldeadl_hotel;
            cancel_text =
              "在" +
              new Date(cancel_r.Day).format("yyyy年MM月dd日") +
              cancel_r.Time +
              "时前可以免费变更取消";
          }
        }
        // console.log(cancel_text);
      }
      if (cancel_r && POG1 == "GuaranteeRuleIds") {
        if (cancel_r.IsTimeGuarantee == "true") {
          var n = cancel_r.EndTime.replace(":", "");
          if (n.length == 3) {
            n = "0" + n;
          }
          if (cancel_r.StartTime.substr(0, 2) * 1 > n.substr(0, 2) * 1) {
            n = 24 + n.substr(0, 2) * 1 + n.substr(2);
          }
          // set_gotime_changed(tar3, cancel_r, n);
        }
      }

      // console.log(cancel_text);
      // cancel_r.cancel_text = cancel_text;
      // irr.cancel_text=cancel_text
      // console.log(cancel_r,irr)
      // console.log(this.hotel_Det)
      //   let roms=this.hotel_Det.Rooms
      // //  console.log()
      //   this.hotel_Det.Rooms.map((item, i, array)=>{
      //    var hs_t=[]
      //     // hs_t.push.apply()///////!!!!
      //     console.log(array[i])
      //   })
      // this.dispo_cancel(cancel_r,cancel_text);
      cancel_r.cancel_text = cancel_text;
      cancel_r.cancel_time = cancel_time;
      //房间规则处理
      if (irr.PaymentType == "Prepay") {
        // console.log("Prepay",irr['PrepayRuleIds'],'cancel_r',cancel_r.PrepayRuleId)
        if (irr["PrepayRuleIds"] == cancel_r.PrepayRuleId) {
          irr.cancel_text = cancel_r.cancel_text;
          irr.cancel_time = cancel_r.cancel_time;
        }
      } else {
        // console.log("SelfPay",irr['GuaranteeRuleIds'],'cancel_r',cancel_r.GuranteeRuleId)
        if (irr["GuaranteeRuleIds"] == cancel_r.GuranteeRuleId) {
          irr.cancel_text = cancel_r.cancel_text;
          irr.cancel_time = cancel_r.cancel_time;
        }
      }
      //

      // console.log(irr);

      // irr.cancel_text = cancel_r.cancel_r;
      // console.log("irr", irr, "cancel_r", cancel_r);

      // setTimeout(() => {
      //   this.$nextTick(() => {
      //     console.log(this.$refs);
      //   });
      // }, 200);

      // this.$toast.clear();
      // this.cancel_time = cancel_time;
      // cancel_r.cancel_time =cancel_time
      return cancel_text;
