      let notice = "";

      if (this.purposeValueObj == "" || this.purposeValueObj == "Select...") {
        go_next = false;
        notice = "请选择差旅目的";
      }
      let last_arrive_val = null;
      console.log('this.last_arrive', this.last_arrive);
      this.last_arrive.map((v) => {
