        return false;
      }

      if (b.hotel_des.PaymentType == "SelfPay") {
        this.bookclick_next_for_db(a, b);
      }

      this.choose_travel_set2(last_arrive_val);
      let p = this.get_data1(a, b);
      console.log(p);
      this.submit_post1(p[0], p[1], p);
