    bookclick_next_for_db () {
      var cardinfo = {
        HolderName: "", //姓名
        IdNo: "",
        IdType: "",
        Number: "", //卡号
        CVV: "", //验证码
        ExpirationMonth: "", //有效期限年月
        ExpirationYear: "", //有效期限
        Organisation: "", //发卡组织
      };
      if (this.Guarantee_card.HolderName) cardinfo = this.Guarantee_card;

      var hotel_accept_ye = this.sethotel_accept_ye();
      if (!cardinfo.HolderName) {
        this.dialogMseeage(
          "请填写持卡人姓名!/Please fill in the Card holder's name."
        );
        return;
      }
      if (false && escape(cardinfo.HolderName).indexOf("%u") != -1) {
        this.dialogMseeage(
          "HRS房源预订时信用卡姓名不能含有汉字!/When booking the HRS hotel, the name of the credit card cannot contain Chinese characters."
        );
        return;
      }
      if (!cardinfo.IdType) {
        this.dialogMseeage("请选择证件信息!/Please select ID information.");
        return;
      }
      if (!cardinfo.IdNo) {
        this.dialogMseeage(
          "请填写持卡人证件号!/Please fill in Card holder’s ID number."
        );
        return;
      }
      //判断发卡组织是否选择
      if (!cardinfo.Organisation && true) {
        this.dialogMseeage(
          "请填写发卡组织!/Please fill in card issuing organization"
        );
        return;
      }
      //判断是否填写信用卡卡号
      if (!cardinfo.Number) {
        this.dialogMseeage("请填写信用卡卡号!/Please fill in Card number.");
        return;
      }
      //判断是否填写信用卡验证码
      if (!cardinfo.CVV) {
        this.dialogMseeage("请填写信用卡验证码!/Please fill in security code.");
        return;
      }
      if (cardinfo.CVV.length != 4) {
        this.dialogMseeage("验证码为四位数!/Security code has three digits");
        return;
      }

      //判断是否选择信用卡有效期-年
      if (cardinfo.ExpirationYear.length != 4) {
        this.dialogMseeage("请填写信用卡有效年份!/Please select Valid year.");
        return;
      }
      //判断是否选择信用卡有效期-月

      // console.log(this.numExpirationMonth(cardinfo.ExpirationMonth))
      cardinfo.ExpirationMonth = this.numExpirationMonth(
        cardinfo.ExpirationMonth
      );
      if (cardinfo.ExpirationMonth.length != 2) {
        this.dialogMseeage("请填写信用卡有效月份!/Please select Valid month.");
        return;
      }
      let pdcard = this.luhnCheck(cardinfo.Number);
      if (pdcard != "卡号通过") {
        this.dialogMseeage(pdcard);
        return;
      }

      sessionStorage.setItem("card", JSON.stringify(cardinfo));
      this.Guarantee_card = cardinfo;
      // console.log(this.Guarantee_card, cardinfo);
    },

