    toMoney (num) {
      num = parseFloat(num);
      num = num + "";

      if (num.indexOf(".") > 0) {
        var x = num.split(".")[1];
        var y = num.split(".")[0];
        if (x.length >= 2) {
          return y + "." + x.substr(0, 2);
        } else if (x.length == 1) {
          return num + "0";
        } else if (x.length == 0) {
          return num + "00";
        }
      } else {
        return num + ".00"; //返回的是字符串23,245.12保留2位小数
      }
