    getQueryString (name) {
      let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      let urlse = decodeURI(location.search);
      let r = urlse.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      return null;
    },
    getOffsetDays (time1, time2) {
      if (time1 > time2) {
        var offsetTime = Math.abs(time1 - time2);
        var dd = -1;
      } else {
        var offsetTime = Math.abs(time2 - time1);
        var dd = 1;
      }

      //   console.log(Math.ceil(offsetTime / (3600 * 24 * 1e3)));
      return dd * Math.ceil(offsetTime / (3600 * 24 * 1e3));
    },
